import React from "react";
import {
  Avatar,
  Box,
  Paper,
  Typography,
  Button,
  IconButton,
  List,
  ListItem,
  ListItemAvatar,
  ListItemText,
  Tabs,
  Tab,
} from "@mui/material";
import { Add, Edit, Delete } from "@mui/icons-material";
import {
  formatCurrency,
  formatDate,
  formatDateParts,
  renderAvatarContent,
  formatFieldData,
} from "../../utils/categoryViewUtils";
import {
  getTransactionIcon,
  getTransactionColor,
} from "../../utils/transactionUtils";
import TransactionHistory from "./common/TransactionHistory";

const InvestmentWebSkeleton = ({
  viewData,
  categoryData,
  onAdd,
  onAction,
  selectedCategoryInfo,
  showValues = true, // Default to true for backward compatibility
  // Transaction history props
  transactionHistory,
  selectedInvestment,
  handleAddTransaction,
  handleEditTransaction,
  handleDeleteTransaction,
  handleViewTransactionHistory,
}) => {
  const [activeTab, setActiveTab] = React.useState(0); // 0: Assets, 1: History
  const [isHistoryEnabled, setIsHistoryEnabled] = React.useState(false);

  // Reset to Assets tab when category changes
  React.useEffect(() => {
    setActiveTab(0);
    setIsHistoryEnabled(false);
  }, [selectedCategoryInfo?.name]);

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleHistoryButtonClick = (item) => {
    setIsHistoryEnabled(true);
    setActiveTab(1);
    // Call the function that sets selected investment without opening popup
    if (handleViewTransactionHistory) {
      handleViewTransactionHistory(item);
    }
  };

  const avatarBg = selectedCategoryInfo.color || "#666";
  return (
    <Box
      sx={{
        flexGrow: 1,
        minWidth: 0,
        transition: "width 0.3s ease-in-out",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Paper
        sx={{
          borderRadius: 2,
          overflow: "hidden",
          boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
          bgcolor: "#1a1a1a",
          border: "1px solid #333",
          flex: 1,
          display: "flex",
          flexDirection: "column",
        }}
      >
        {/* Tabs Header */}
        <Box sx={{ bgcolor: "#2d2d2d", borderBottom: "1px solid #444" }}>
          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            sx={{
              "& .MuiTab-root": {
                color: "#aaa",
                fontWeight: 600,
                "&.Mui-selected": {
                  color: "white",
                },
              },
              "& .MuiTabs-indicator": {
                bgcolor: selectedCategoryInfo?.color || "#f0a500",
              },
            }}
          >
            <Tab label="Assets" disabled={false} />
            <Tab
              label="History"
              disabled={!isHistoryEnabled}
              sx={{
                "&.Mui-disabled": {
                  color: "#555",
                },
              }}
            />
          </Tabs>
        </Box>

        {/* Tab Content */}
        <Box sx={{ flex: 1, overflow: "hidden" }}>
          {activeTab === 0 && (
            <Box
              sx={{ height: "100%", display: "flex", flexDirection: "column" }}
            >
              {/* Assets Tab Content */}
              <Box
                sx={{
                  p: 3,
                  bgcolor: "#2d2d2d",
                  borderBottom: "1px solid #444",
                }}
              >
                {/* Summary - First Line with Add Button */}
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "flex-start",
                    mb: 3,
                    gap: 2,
                  }}
                >
                  <Box
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      gap: 2,
                      flex: 1,
                    }}
                  >
                    <Avatar
                      sx={{
                        bgcolor: avatarBg,
                        width: 48,
                        height: 48,
                        fontSize: "1.5rem",
                      }}
                    >
                      {renderAvatarContent(
                        selectedCategoryInfo?.name,
                        selectedCategoryInfo?.icon
                      )}
                    </Avatar>
                    <Box sx={{ flex: 1 }}>
                      <Typography
                        variant="h5"
                        sx={{ fontWeight: 600, color: "white", mb: 0.5 }}
                      >
                        {viewData.header.title}
                      </Typography>
                      <Typography variant="body2" sx={{ color: "#aaa" }}>
                        {viewData.header.description}
                      </Typography>
                    </Box>
                  </Box>
                  {viewData.header.showAddButton && (
                    <Button
                      variant="contained"
                      startIcon={<Add />}
                      onClick={onAdd}
                      sx={{
                        bgcolor: "#4caf50",
                        "&:hover": {
                          bgcolor: "#1565c0",
                          filter: "invert(100%)",
                        },
                        fontSize: "1rem",
                        fontWeight: 800,
                        flexShrink: 0,
                      }}
                    >
                      Add
                    </Button>
                  )}
                </Box>

                {/* Summary - Second Line Overview */}
                <Box
                  sx={{
                    display: "grid",
                    gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
                    gap: 3,
                    width: "100%",
                  }}
                >
                  {viewData.summary.map((summary, idx) => {
                    const fontSize = summary.fontSize || "2rem";
                    const { formattedValue: value, formattedColor: color } =
                      formatFieldData(summary, categoryData, showValues);

                    // Support summary.icon (optional)
                    const Icon = summary.icon || null;
                    return (
                      <Box
                        key={summary.label}
                        sx={{
                          display: "flex",
                          flexDirection: "column",
                          alignItems:
                            summary.type === "date"
                              ? "center"
                              : idx === 0
                              ? "flex-start"
                              : idx === 1
                              ? "center"
                              : "flex-end",
                        }}
                      >
                        <Typography
                          variant="body2"
                          sx={{ color: "#aaa", mb: 1 }}
                        >
                          {summary.label}
                        </Typography>
                        <Typography
                          variant="h4"
                          sx={{
                            fontWeight: 700,
                            color,
                            fontSize,
                            display: "flex",
                            alignItems: "center",
                            paddingRight: 2,
                            ...(summary.type === "date" && {
                              color,
                              fontWeight: 500,
                              fontSize: "1.1rem",
                              display: "flex",
                              flexDirection: "column",
                              alignItems: "center",
                              p: 0,
                              m: 0,
                            }),
                          }}
                        >
                          {summary.type === "date" ? (
                            (() => {
                              const { dayMonth, year } = formatDateParts(
                                categoryData[summary.valueKey]
                              );
                              // Use fontSize from JSON for dayMonth, 80% for year
                              const mainFontSize = parseFloat(fontSize) || 1.1;
                              const mainFontUnit =
                                (fontSize && fontSize.replace(/[\d.]/g, "")) ||
                                "rem";
                              const yearFontSize =
                                (mainFontSize * 0.8).toFixed(2) + mainFontUnit;
                              return (
                                <>
                                  <span
                                    style={{
                                      fontSize: fontSize,
                                      fontWeight: 500,
                                      textAlign: "center",
                                      display: "block",
                                    }}
                                  >
                                    {dayMonth}
                                  </span>
                                  <span
                                    style={{
                                      fontSize: yearFontSize,
                                      color: "#aaa",
                                      fontWeight: 400,
                                      marginTop: 2,
                                      textAlign: "center",
                                      display: "block",
                                    }}
                                  >
                                    {year}
                                  </span>
                                </>
                              );
                            })()
                          ) : (
                            <>
                              {Icon && (
                                <span
                                  style={{
                                    verticalAlign: "middle",
                                    marginRight: 4,
                                  }}
                                >
                                  {Icon}
                                </span>
                              )}
                              {value}
                            </>
                          )}
                        </Typography>
                      </Box>
                    );
                  })}
                </Box>
              </Box>

              {/* Investments List */}
              <Box
                sx={{
                  p: 3,
                  flex: 1,
                  overflow: "auto",
                }}
              >
                {categoryData.items && categoryData.items.length > 0 ? (
                  <List disablePadding>
                    {categoryData.items.map((item, index) => (
                      <React.Fragment key={item.id || index}>
                        <ListItem
                          sx={{
                            px: 0,
                            py: 2,
                            flexDirection: "column",
                            alignItems: "flex-start",
                          }}
                        >
                          <Box
                            sx={{
                              display: "flex",
                              alignItems: "center",
                              width: "100%",
                              mb: 1,
                              gap: 2,
                            }}
                          >
                            <ListItemAvatar>
                              <Avatar
                                sx={{
                                  bgcolor: avatarBg,
                                  width: 48,
                                  height: 48,
                                  fontSize: "1.2rem",
                                }}
                              >
                                {renderAvatarContent(
                                  selectedCategoryInfo?.name,
                                  selectedCategoryInfo?.icon
                                )}
                              </Avatar>
                            </ListItemAvatar>
                            <ListItemText
                              primary={
                                <Typography
                                  variant="h6"
                                  sx={{ fontWeight: 600, color: "white" }}
                                >
                                  {item.name}
                                </Typography>
                              }
                              secondary={
                                <Typography
                                  variant="body2"
                                  sx={{ color: "#aaa" }}
                                >
                                  Invested:{" "}
                                  {formatCurrency(
                                    item.investedAmount,
                                    showValues
                                  )}
                                </Typography>
                              }
                              sx={{ flex: 1, minWidth: 0 }}
                            />
                            <Box
                              sx={{ display: "flex", gap: 1, flexShrink: 0 }}
                            >
                              {viewData.items.actions?.map((action) => {
                                if (action.key === "history") {
                                  return (
                                    <IconButton
                                      key={action.key}
                                      onClick={() =>
                                        handleHistoryButtonClick(item)
                                      }
                                      sx={{ color: "#4cffd3" }}
                                      title={action.label}
                                      size="small"
                                    >
                                      {action.icon}
                                    </IconButton>
                                  );
                                } else if (action.key === "edit") {
                                  return (
                                    <IconButton
                                      key={action.key}
                                      onClick={() =>
                                        onAction?.(action.key, item)
                                      }
                                      sx={{ color: "#f0a500" }}
                                      title={action.label}
                                      size="small"
                                    >
                                      <Edit />
                                    </IconButton>
                                  );
                                } else if (action.key === "delete") {
                                  return (
                                    <IconButton
                                      key={action.key}
                                      onClick={() =>
                                        onAction?.(action.key, item)
                                      }
                                      sx={{ color: "#f44336" }}
                                      title={action.label}
                                      size="small"
                                    >
                                      <Delete />
                                    </IconButton>
                                  );
                                }
                                return null;
                              })}
                            </Box>
                          </Box>
                          <Box
                            sx={{
                              display: "grid",
                              gridTemplateColumns: "1fr auto 1fr",
                              gap: 3,
                              width: "100%",
                              alignItems: "start",
                            }}
                          >
                            {viewData.items.fields.map((field, idx) => {
                              const {
                                formattedValue: value,
                                formattedColor: color,
                              } = formatFieldData(field, item, showValues);
                              return (
                                <Box
                                  key={field.label}
                                  sx={{
                                    display: "flex",
                                    flexDirection: "column",
                                    alignItems:
                                      idx === 0
                                        ? "flex-start"
                                        : idx === 1
                                        ? "center"
                                        : "flex-end",
                                  }}
                                >
                                  <Typography
                                    variant="body2"
                                    sx={{ color: "#aaa", mb: 1 }}
                                  >
                                    {field.label}
                                  </Typography>
                                  <Typography
                                    variant={
                                      field.type === "currencyHighlight"
                                        ? "h5"
                                        : "h6"
                                    }
                                    sx={{
                                      fontWeight:
                                        field.type === "currencyHighlight"
                                          ? 700
                                          : 600,
                                      color,
                                      textShadow:
                                        field.type === "currencyHighlight"
                                          ? "0 0 10px rgba(255,255,255,0.3)"
                                          : undefined,
                                      background:
                                        field.type === "currencyHighlight"
                                          ? "linear-gradient(45deg, #fff, #f0f0f0)"
                                          : undefined,
                                      backgroundClip:
                                        field.type === "currencyHighlight"
                                          ? "text"
                                          : undefined,
                                      WebkitBackgroundClip:
                                        field.type === "currencyHighlight"
                                          ? "text"
                                          : undefined,
                                      WebkitTextFillColor:
                                        field.type === "currencyHighlight"
                                          ? "transparent"
                                          : undefined,
                                    }}
                                  >
                                    {value}
                                  </Typography>
                                </Box>
                              );
                            })}
                          </Box>
                        </ListItem>
                        {index < categoryData.items.length - 1 && (
                          <Box sx={{ height: 1, bgcolor: "#444", mx: 2 }} />
                        )}
                      </React.Fragment>
                    ))}
                  </List>
                ) : (
                  <Box sx={{ textAlign: "center", py: 4 }}>
                    <Typography variant="body2" sx={{ color: "#aaa" }}>
                      {viewData.items.emptyText}
                    </Typography>
                  </Box>
                )}
              </Box>
            </Box>
          )}
          {activeTab === 1 && (
            <Box
              sx={{ height: "100%", display: "flex", flexDirection: "column" }}
            >
              {selectedInvestment ? (
                <Box sx={{ height: "100%" }}>
                  <TransactionHistory
                    selectedInvestment={selectedInvestment}
                    transactionHistory={transactionHistory}
                    handleAddTransaction={handleAddTransaction}
                    handleEditTransaction={handleEditTransaction}
                    handleDeleteTransaction={handleDeleteTransaction}
                    formatCurrency={(amount) =>
                      formatCurrency(amount, showValues)
                    }
                    formatDate={(dateValue) =>
                      formatDate(dateValue, showValues)
                    }
                    getTransactionIcon={getTransactionIcon}
                    getTransactionColor={getTransactionColor}
                    onClose={() => {
                      setActiveTab(0);
                      setIsHistoryEnabled(false);
                    }}
                  />
                </Box>
              ) : (
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "center",
                    justifyContent: "center",
                    height: "100%",
                    p: 4,
                    textAlign: "center",
                  }}
                >
                  <Typography variant="h6" sx={{ color: "white", mb: 2 }}>
                    Transaction History
                  </Typography>
                  <Typography variant="body2" sx={{ color: "#aaa", mb: 3 }}>
                    Click on the history button of any investment to view its
                    transaction history
                  </Typography>
                  <Button
                    variant="outlined"
                    onClick={() => setActiveTab(0)}
                    sx={{
                      borderColor: "#f0a500",
                      color: "#f0a500",
                      "&:hover": {
                        borderColor: "#d89400",
                        bgcolor: "rgba(240, 165, 0, 0.1)",
                      },
                    }}
                  >
                    Back to Assets
                  </Button>
                </Box>
              )}
            </Box>
          )}
        </Box>
      </Paper>
    </Box>
  );
};

export default InvestmentWebSkeleton;
