import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  IconButton,
  Chip,
  FormControl,
  InputLabel,
  Select,
  MenuItem,
  useMediaQuery,
  useTheme,
  Autocomplete,
} from "@mui/material";
import { Close, Add, Edit, TrendingUp } from "@mui/icons-material";
import { DASHBOARD } from "../../../constants/dashboard";

const InvestmentAmendDialogSkeleton = ({
  dialogOpen,
  dialogType,
  formData = {},
  setFormData,
  handleSave,
  handleClose,
  selectedCategoryInfo,
  amendConfig,
  categoryKey,
}) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  const isEdit = dialogType === "edit";
  const title = isEdit ? amendConfig.title.edit : amendConfig.title.add;
  const buttonText = isEdit
    ? amendConfig.buttonText.edit
    : amendConfig.buttonText.add;

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleSubmit = () => {
    const investmentData = {
      ...formData,
      category: categoryKey,
      investedAmount: parseFloat(formData.investedAmount),
      currentValue: parseFloat(formData.currentValue),
    };
    handleSave(investmentData);
    handleClose();
  };

  const isFormValid = () => {
    return amendConfig.fields.every((field) => {
      if (!field.required) return true;
      if (field.type === "number")
        return (
          formData[field.name] !== "" &&
          formData[field.name] !== undefined &&
          formData[field.name] !== null
        );
      return formData[field.name]?.toString().trim();
    });
  };

  const renderField = (field) => {
    const commonSx = {
      "& .MuiOutlinedInput-root": {
        color: "white",
        borderRadius: 1,
        minHeight: "56px",
        "& fieldset": {
          borderColor: "#444",
        },
        "&:hover fieldset": {
          borderColor: "#f0a500",
        },
        "&.Mui-focused fieldset": {
          borderColor: "#f0a500",
        },
        "& input": {
          color: "white",
          height: "56px",
          padding: "16.5px 14px",
        },
        "& textarea": {
          color: "white",
          padding: "16.5px 14px",
        },
      },
      "& .MuiInputLabel-root": {
        color: "#aaa",
        fontWeight: 500,
        "&.Mui-focused": {
          color: "#f0a500",
        },
      },
    };

    // Get autocomplete options based on field name
    const getAutocompleteOptions = (fieldName) => {
      return DASHBOARD.AUTOCOMPLETE_VALUES[fieldName] || [];
    };

    // Handle autocomplete text fields
    if (
      field.type === "text" &&
      getAutocompleteOptions(field.name).length > 0
    ) {
      return (
        <Autocomplete
          freeSolo
          options={getAutocompleteOptions(field.name)}
          value={formData[field.name] || ""}
          onChange={(event, newValue) => {
            setFormData((prev) => ({
              ...prev,
              [field.name]: newValue || "",
            }));
          }}
          onInputChange={(event, newInputValue) => {
            setFormData((prev) => ({
              ...prev,
              [field.name]: newInputValue,
            }));
          }}
          renderInput={(params) => (
            <TextField
              {...params}
              label={field.label}
              required={field.required}
              placeholder={field.placeholder}
              sx={{
                ...commonSx,
                "& .MuiAutocomplete-input": {
                  color: "white",
                },
                "& .MuiAutocomplete-popupIndicator": {
                  color: "#aaa",
                },
                "& .MuiAutocomplete-clearIndicator": {
                  color: "#aaa",
                },
              }}
            />
          )}
          sx={{
            "& .MuiAutocomplete-popper": {
              "& .MuiPaper-root": {
                bgcolor: "#2d2d2d",
                border: "1px solid #444",
              },
              "& .MuiAutocomplete-option": {
                color: "white",
                "&:hover": {
                  bgcolor: "#444",
                },
                "&.Mui-focused": {
                  bgcolor: "#f0a500",
                  color: "black",
                },
              },
            },
          }}
        />
      );
    }

    // Handle date fields
    if (field.type === "date") {
      return (
        <TextField
          name={field.name}
          fullWidth
          label={field.label}
          type="date"
          value={
            formData[field.name] !== undefined
              ? new Date(formData[field.name]).toISOString().split("T")[0]
              : field.default
              ? typeof field.default === "function"
                ? field.default()
                : field.default
              : ""
          }
          onChange={handleInputChange}
          required={field.required}
          placeholder={field.placeholder}
          InputLabelProps={{ shrink: true }}
          InputProps={{
            style: { color: "white" },
          }}
          sx={{
            ...commonSx,
            "& .MuiOutlinedInput-root": {
              ...commonSx["& .MuiOutlinedInput-root"],
              cursor: "pointer",
              "& input": {
                color: "white",
                cursor: "pointer",
              },
              '& input[type="date"]::-webkit-calendar-picker-indicator': {
                filter: "invert(1)",
                cursor: "pointer",
              },
            },
          }}
        />
      );
    }

    // Handle select fields
    if (field.type === "select" && field.options) {
      return (
        <FormControl fullWidth>
          <InputLabel sx={{ color: "#aaa", fontWeight: 500 }}>
            {field.label}
          </InputLabel>
          <Select
            name={field.name}
            value={formData[field.name] || ""}
            onChange={handleInputChange}
            sx={{
              color: "white",
              minHeight: "56px",
              "& .MuiOutlinedInput-notchedOutline": {
                borderColor: "#444",
                borderRadius: 1,
              },
              "&:hover .MuiOutlinedInput-notchedOutline": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused .MuiOutlinedInput-notchedOutline": {
                borderColor: "#f0a500",
              },
              "& .MuiSvgIcon-root": {
                color: "#aaa",
              },
            }}
          >
            {field.options.map((option) => (
              <MenuItem key={option.value} value={option.value}>
                {option.label}
              </MenuItem>
            ))}
          </Select>
        </FormControl>
      );
    }

    // Handle textarea fields
    if (field.type === "textarea") {
      return (
        <TextField
          name={field.name}
          fullWidth
          label={field.label}
          multiline
          rows={field.rows || 4}
          value={
            formData[field.name] !== undefined
              ? formData[field.name]
              : field.default
              ? typeof field.default === "function"
                ? field.default()
                : field.default
              : ""
          }
          onChange={handleInputChange}
          required={field.required}
          placeholder={field.placeholder}
          sx={{
            ...commonSx,
            "& .MuiOutlinedInput-root": {
              ...commonSx["& .MuiOutlinedInput-root"],
              minHeight: "120px",
              "& textarea": {
                padding: "16.5px 14px",
                lineHeight: "1.5",
              },
            },
          }}
        />
      );
    }

    // Default text/number fields
    return (
      <TextField
        name={field.name}
        fullWidth
        label={field.label}
        type={field.type}
        value={
          formData[field.name] !== undefined
            ? formData[field.name]
            : field.default
            ? typeof field.default === "function"
              ? field.default()
              : field.default
            : ""
        }
        onChange={handleInputChange}
        required={field.required}
        placeholder={field.placeholder}
        sx={commonSx}
      />
    );
  };

  return (
    <Dialog
      open={dialogOpen}
      onClose={handleClose}
      maxWidth="md"
      fullWidth
      fullScreen={isMobile}
      PaperProps={{
        sx: {
          bgcolor: "#1a1a1a",
          color: "white",
          border: "1px solid #333",
          borderRadius: isMobile ? 0 : 2,
          m: isMobile ? 0 : 2,
        },
      }}
    >
      {/* Header */}
      <DialogTitle
        sx={{
          borderBottom: "1px solid #333",
          bgcolor: "#2d2d2d",
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          p: 2,
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
          {isEdit ? (
            <Edit sx={{ color: "#f0a500" }} />
          ) : (
            <Add sx={{ color: "#4caf50" }} />
          )}
          <Typography variant="h6" sx={{ fontWeight: 600 }}>
            {title}
          </Typography>
        </Box>
        <IconButton
          onClick={handleClose}
          sx={{
            color: "#aaa",
            "&:hover": { bgcolor: "rgba(255,255,255,0.1)" },
          }}
        >
          <Close />
        </IconButton>
      </DialogTitle>

      {/* Category Info */}
      <Box
        sx={{
          p: 2,
          bgcolor: "#2d2d2d",
          borderBottom: "1px solid #333",
          display: "flex",
          alignItems: "center",
          gap: 2,
        }}
      >
        <TrendingUp sx={{ color: selectedCategoryInfo?.color || "#666" }} />
        <Typography variant="body2" sx={{ color: "#aaa" }}>
          Category:
        </Typography>
        <Chip
          label={selectedCategoryInfo?.name}
          sx={{
            bgcolor: selectedCategoryInfo?.color || "#444",
            color: "white",
            fontWeight: 600,
          }}
        />
      </Box>

      {/* Form Content */}
      <DialogContent sx={{ p: isMobile ? 2 : 3, flex: 1 }}>
        <Box sx={{ pt: isMobile ? 0 : 1 }}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              gap: isMobile ? 2 : 3,
            }}
          >
            {amendConfig.fields.map((field) => (
              <Box key={field.name} sx={{ width: "100%" }}>
                {renderField(field)}
              </Box>
            ))}
          </Box>
        </Box>
      </DialogContent>

      {/* Actions */}
      <DialogActions
        sx={{
          borderTop: "1px solid #333",
          bgcolor: "#2d2d2d",
          p: isMobile ? 2 : 3,
          gap: 2,
          flexDirection: isMobile ? "column" : "row",
        }}
      >
        <Button
          onClick={handleClose}
          variant="outlined"
          fullWidth={isMobile}
          sx={{
            color: "#aaa",
            borderColor: "#444",
            "&:hover": {
              borderColor: "#aaa",
              bgcolor: "rgba(255,255,255,0.05)",
            },
          }}
        >
          Cancel
        </Button>
        <Button
          onClick={handleSubmit}
          disabled={!isFormValid()}
          variant="contained"
          fullWidth={isMobile}
          sx={{
            bgcolor: isEdit ? "#f0a500" : "#4caf50",
            color: isEdit ? "black" : "white",
            fontWeight: 600,
            px: isMobile ? 2 : 3,
            py: isMobile ? 1.5 : 1,
            "&:hover": {
              bgcolor: isEdit ? "#d89400" : "#45a049",
            },
            "&:disabled": {
              bgcolor: "#666",
              color: "#aaa",
            },
          }}
        >
          {buttonText}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default InvestmentAmendDialogSkeleton;
