import React, { useState } from "react";
import {
  Box,
  Typography,
  List,
  ListItem,
  IconButton,
  Avatar,
  ListItemAvatar,
} from "@mui/material";
import { Add } from "@mui/icons-material";
import {
  formatCurrency,
  formatDateParts,
  renderAvatarContent,
  formatFieldData,
} from "../../utils/categoryViewUtils";

const InvestmentMobileSkeleton = ({
  viewData,
  categoryData,
  onAdd,
  onAction,
  selectedCategoryInfo,
  showValues = true, // Default to true for backward compatibility
}) => {
  const avatarBg = selectedCategoryInfo?.color || "#666";
  // Track swipe offset per item
  const [swipeOffsets, setSwipeOffsets] = useState({});
  const [swipeStart, setSwipeStart] = useState(null);
  const [swipedItem, setSwipedItem] = useState(null);

  const handleSwipeStart = (e, itemId) => {
    const clientX = e.touches ? e.touches[0].clientX : e.clientX;
    setSwipeStart(clientX);

    // Close other items when starting to swipe a new item
    if (swipedItem !== itemId) {
      setSwipeOffsets((prev) => {
        const newOffsets = {};
        Object.keys(prev).forEach((key) => {
          if (key !== itemId.toString()) {
            newOffsets[key] = 0;
          }
        });
        return newOffsets;
      });
    }

    setSwipedItem(itemId);
  };

  const handleSwipeMove = (e, itemId) => {
    if (swipedItem !== itemId || swipeStart === null) return;
    const clientX = e.touches ? e.touches[0].clientX : e.clientX;
    const offset = swipeStart - clientX;

    // Allow both left and right swipe
    if (offset > 0) {
      // Left swipe - open menu
      setSwipeOffsets((prev) => ({
        ...prev,
        [itemId]: Math.min(offset, 80),
      }));
    } else if (offset < 0) {
      // Right swipe - close menu
      const currentOffset = swipeOffsets[itemId] || 0;
      const newOffset = Math.max(currentOffset + offset, 0);
      setSwipeOffsets((prev) => ({
        ...prev,
        [itemId]: newOffset,
      }));
    }
  };

  const handleSwipeEnd = (itemId) => {
    if (swipedItem !== itemId) return;
    const offset = swipeOffsets[itemId] || 0;

    if (offset > 40) {
      // Keep menu open
      setSwipeOffsets((prev) => ({ ...prev, [itemId]: 80 }));
    } else {
      // Close menu
      setSwipeOffsets((prev) => ({ ...prev, [itemId]: 0 }));
      setSwipedItem(null);
    }
    setSwipeStart(null);
  };

  const handleActionClick = (actionKey, item) => {
    // Close all swipe menus when action is clicked
    setSwipeOffsets({});
    setSwipedItem(null);
    setSwipeStart(null);
    onAction?.(actionKey, item);
  };

  return (
    <Box sx={{ height: "100%", overflow: "visible" }}>
      {/* Header */}
      <Box sx={{ p: 3, bgcolor: "#2d2d2d", borderBottom: "1px solid #444" }}>
        <Box sx={{ display: "flex", alignItems: "center", gap: 2, mb: 2 }}>
          <Avatar
            sx={{
              bgcolor: avatarBg,
              width: 48,
              height: 48,
              fontSize: "1.5rem",
              flexShrink: 0,
            }}
          >
            {renderAvatarContent(
              selectedCategoryInfo?.name,
              selectedCategoryInfo?.icon
            )}
          </Avatar>
          <Box sx={{ flex: 1 }}>
            <Typography variant="h5" sx={{ fontWeight: 600, color: "white" }}>
              {viewData.header.title}
            </Typography>
            <Typography variant="body2" sx={{ color: "#aaa" }}>
              {viewData.header.description}
            </Typography>
          </Box>
          {viewData.header.showAddButton && (
            <IconButton
              onClick={onAdd}
              sx={{
                bgcolor: "#4caf50",
                color: "white",
                px: 3,
                height: 40,
                borderRadius: "20px",
                boxShadow: 2,
                fontWeight: 600,
                "&:hover": {
                  bgcolor: "#4caf50",
                  filter: "brightness(1.1)",
                },
                flexShrink: 0,
              }}
              size="large"
              aria-label="Add"
            >
              <Add fontSize="inherit" />
            </IconButton>
          )}
        </Box>
        <Box
          sx={{
            display: "flex",
            justifyContent: "space-between",
            alignItems: "center",
            gap: 2,
          }}
        >
          {viewData.summary.map((summary, idx) => {
            const fontSize = summary.fontSize || "2rem";
            const { formattedValue: value, formattedColor: color } =
              formatFieldData(summary, categoryData, showValues);
            const Icon = summary.icon || null;
            return (
              <Box
                key={summary.label}
                sx={{
                  display: "flex",
                  flexDirection: "column",
                  alignItems:
                    summary.type === "date"
                      ? "center"
                      : idx === 0
                      ? "flex-start"
                      : idx === 1
                      ? "center"
                      : "flex-end",
                }}
              >
                <Typography variant="body2" sx={{ color: "#aaa", mb: 1 }}>
                  {summary.label}
                </Typography>
                <Box
                  component="div"
                  sx={{
                    fontWeight: 700,
                    color,
                    fontSize,
                    display: "flex",
                    alignItems: "center",
                    paddingRight: 2,
                    lineHeight: 1.2,
                    ...(summary.type === "date" && {
                      color,
                      fontWeight: 500,
                      fontSize: "1.1rem",
                      display: "flex",
                      flexDirection: "column",
                      alignItems: "center",
                      p: 0,
                      m: 0,
                    }),
                  }}
                >
                  {summary.type === "date" ? (
                    (() => {
                      const { dayMonth, year } = formatDateParts(
                        categoryData[summary.valueKey]
                      );
                      const mainFontSize = parseFloat(fontSize) || 1.1;
                      const mainFontUnit =
                        (fontSize && fontSize.replace(/[\d.]/g, "")) || "rem";
                      const yearFontSize =
                        (mainFontSize * 0.8).toFixed(2) + mainFontUnit;
                      return (
                        <>
                          <span
                            style={{
                              fontSize: fontSize,
                              fontWeight: 500,
                              textAlign: "center",
                              display: "block",
                            }}
                          >
                            {dayMonth}
                          </span>
                          <span
                            style={{
                              fontSize: yearFontSize,
                              color: "#aaa",
                              fontWeight: 400,
                              marginTop: 2,
                              textAlign: "center",
                              display: "block",
                            }}
                          >
                            {year}
                          </span>
                        </>
                      );
                    })()
                  ) : (
                    <>
                      {Icon && (
                        <span
                          style={{ verticalAlign: "middle", marginRight: 4 }}
                        >
                          {Icon}
                        </span>
                      )}
                      {value}
                    </>
                  )}
                </Box>
              </Box>
            );
          })}
        </Box>
      </Box>
      {/* List */}
      <Box sx={{ flex: 1, overflow: "auto" }}>
        {categoryData.items && categoryData.items.length > 0 ? (
          <List>
            {categoryData.items.map((item, index) => {
              const itemId = item.id || index;
              const offset = swipeOffsets[itemId] || 0;
              return (
                <Box
                  key={itemId}
                  sx={{
                    position: "relative",
                    overflow: "hidden",
                    borderBottom: "1px solid #444",
                  }}
                >
                  {/* Action buttons background */}
                  <Box
                    sx={{
                      position: "absolute",
                      right: 0,
                      top: 0,
                      bottom: 0,
                      width: 80,
                      display: "flex",
                      flexDirection: "column",
                      justifyContent: "center",
                      alignItems: "center",
                      bgcolor: "#1a1a1a",
                      zIndex: 1,
                      transform: `translateX(${offset ? 0 : 80}px)`,
                      transition: "transform 0.2s ease-out",
                    }}
                  >
                    {viewData.items.actions?.map((action) => {
                      if (["history", "edit", "delete"].includes(action.key)) {
                        return (
                          <IconButton
                            key={action.key}
                            onClick={() => handleActionClick(action.key, item)}
                            sx={{
                              color:
                                action.key === "history"
                                  ? "#4cffd3"
                                  : action.key === "delete"
                                  ? "#f44336"
                                  : "#f0a500",
                              bgcolor: "rgba(255,255,255,0.1)",
                              width: 40,
                              height: 40,
                              mb: 1,
                              "&:hover": {
                                bgcolor: "rgba(255,255,255,0.2)",
                              },
                            }}
                            title={action.label}
                          >
                            {action.icon}
                          </IconButton>
                        );
                      }
                      return null;
                    })}
                  </Box>
                  {/* Main content */}
                  <ListItem
                    sx={{
                      flexDirection: "column",
                      alignItems: "flex-start",
                      py: 2,
                      px: 3,
                      bgcolor: "#2d2d2d",
                      transform: `translateX(-${offset}px)`,
                      transition: offset ? "none" : "transform 0.2s ease-out",
                      cursor: "grab",
                      "&:active": {
                        cursor: "grabbing",
                      },
                    }}
                    onMouseDown={(e) => handleSwipeStart(e, itemId)}
                    onMouseMove={(e) => handleSwipeMove(e, itemId)}
                    onMouseUp={() => handleSwipeEnd(itemId)}
                    onMouseLeave={() => handleSwipeEnd(itemId)}
                    onTouchStart={(e) => handleSwipeStart(e, itemId)}
                    onTouchMove={(e) => handleSwipeMove(e, itemId)}
                    onTouchEnd={() => handleSwipeEnd(itemId)}
                  >
                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "center",
                        width: "100%",
                        mb: 1,
                        gap: 2,
                      }}
                    >
                      <ListItemAvatar>
                        <Avatar
                          sx={{
                            bgcolor: avatarBg,
                            width: 48,
                            height: 48,
                            fontSize: "1.2rem",
                          }}
                        >
                          {renderAvatarContent(
                            selectedCategoryInfo?.name,
                            selectedCategoryInfo?.icon
                          )}
                        </Avatar>
                      </ListItemAvatar>
                      <Box sx={{ flex: 1, minWidth: 0 }}>
                        {/* Title on top */}
                        <Typography
                          variant="h6"
                          sx={{
                            fontWeight: 600,
                            color: "white",
                            mb: 0.5,
                            whiteSpace: "nowrap",
                            overflow: "hidden",
                            textOverflow: "ellipsis",
                          }}
                        >
                          {item.name}
                        </Typography>
                        {/* Invested amount */}
                        <Typography variant="body2" sx={{ color: "#aaa" }}>
                          Invested:{" "}
                          {formatCurrency(item.investedAmount, showValues)}
                        </Typography>
                      </Box>
                    </Box>
                    <Box
                      sx={{
                        display: "grid",
                        gridTemplateColumns: "1fr auto 1fr",
                        gap: 3,
                        width: "100%",
                        alignItems: "start",
                      }}
                    >
                      {viewData.items.fields.map((field, idx) => {
                        const { formattedValue: value, formattedColor: color } =
                          formatFieldData(field, item, showValues);
                        return (
                          <Box
                            key={field.label}
                            sx={{
                              display: "flex",
                              flexDirection: "column",
                              alignItems:
                                idx === 0
                                  ? "flex-start"
                                  : idx === 1
                                  ? "center"
                                  : "flex-end",
                            }}
                          >
                            <Typography
                              variant="body2"
                              sx={{ color: "#aaa", mb: 1 }}
                            >
                              {field.label}
                            </Typography>
                            <Typography
                              variant={
                                field.type === "currencyHighlight" ? "h5" : "h6"
                              }
                              sx={{
                                fontWeight:
                                  field.type === "currencyHighlight"
                                    ? 700
                                    : 600,
                                color,
                                textShadow:
                                  field.type === "currencyHighlight"
                                    ? "0 0 10px rgba(255,255,255,0.3)"
                                    : undefined,
                                background:
                                  field.type === "currencyHighlight"
                                    ? "linear-gradient(45deg, #fff, #f0f0f0)"
                                    : undefined,
                                backgroundClip:
                                  field.type === "currencyHighlight"
                                    ? "text"
                                    : undefined,
                                WebkitBackgroundClip:
                                  field.type === "currencyHighlight"
                                    ? "text"
                                    : undefined,
                                WebkitTextFillColor:
                                  field.type === "currencyHighlight"
                                    ? "transparent"
                                    : undefined,
                              }}
                            >
                              {value}
                            </Typography>
                          </Box>
                        );
                      })}
                    </Box>
                  </ListItem>
                </Box>
              );
            })}
          </List>
        ) : (
          <Box sx={{ textAlign: "center", py: 4 }}>
            <Typography variant="body2" sx={{ color: "#aaa" }}>
              {viewData.items.emptyText}
            </Typography>
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default InvestmentMobileSkeleton;
