import React from 'react';
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  Typography,
} from '@mui/material';
import { Warning } from '@mui/icons-material';

const DeleteConfirmationDialog = ({
  deleteDialogOpen,
  selectedInvestment,
  handleConfirmDelete,
  handleClose,
}) => {
  return (
    <Dialog 
      open={deleteDialogOpen} 
      onClose={handleClose}
      maxWidth="sm"
      fullWidth
      PaperProps={{
        sx: {
          bgcolor: '#1a1a1a',
          color: 'white',
          border: '1px solid #333',
        },
      }}
    >
      <DialogTitle sx={{ 
        borderBottom: '1px solid #333',
        bgcolor: '#2d2d2d',
        display: 'flex',
        alignItems: 'center',
        gap: 2
      }}>
        <Warning sx={{ color: '#f44336' }} />
        Confirm Delete
      </DialogTitle>
      
      <DialogContent sx={{ pt: 3 }}>
        <Typography variant="body1" sx={{ mb: 2 }}>
          Are you sure you want to delete the investment "{selectedInvestment?.name}"?
        </Typography>
        <Typography variant="body2" sx={{ color: '#aaa' }}>
          This action cannot be undone. All investment data will be permanently removed.
        </Typography>
      </DialogContent>
      
      <DialogActions sx={{ 
        borderTop: '1px solid #333',
        bgcolor: '#2d2d2d',
        p: 2
      }}>
        <Button 
          onClick={handleClose}
          sx={{ 
            color: '#aaa',
            '&:hover': {
              bgcolor: 'rgba(255,255,255,0.1)',
            },
          }}
        >
          Cancel
        </Button>
        <Button 
          onClick={handleConfirmDelete}
          variant="contained"
          sx={{ 
            bgcolor: '#f44336',
            color: 'white',
            '&:hover': {
              bgcolor: '#d32f2f',
            },
          }}
        >
          Delete
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default DeleteConfirmationDialog; 