import React, { useState } from 'react';
import {
  Box,
  Card,
  CardContent,
  Typography,
  IconButton,
  Chip,
  Grid,
  Select,
  MenuItem,
  FormControl,
} from '@mui/material';
import {
  Visibility,
  VisibilityOff,
  TrendingUp,
  TrendingDown,
  Close,
} from '@mui/icons-material';

const PortfolioSummary = ({ 
  portfolioSummary, 
  showValues, 
  setShowValues, 
  formatCompactCurrency, 
  formatCurrency, 
  formatPercentage,
  assetAllocation,
  performanceHistory 
}) => {
  const [timeframe, setTimeframe] = useState('daily');
  const [selectedDataPoint, setSelectedDataPoint] = useState(null);

  // Get performance data based on selected timeframe
  const getPerformanceData = () => {
    if (!performanceHistory) return [];
    
    switch (timeframe) {
      case 'daily':
        return performanceHistory.daily || [];
      case 'monthly':
        return performanceHistory.monthly || [];
      case 'yearly':
        return performanceHistory.yearly || [];
      default:
        return performanceHistory.daily || [];
    }
  };

  const performanceData = getPerformanceData();

  // Handle data point selection for mobile
  const handleDataPointSelect = (dataPoint, index) => {
    setSelectedDataPoint({ ...dataPoint, index });
  };

  // Format date based on timeframe
  const formatDataPointDate = (dataPoint) => {
    if (timeframe === 'daily') {
      return new Date(dataPoint.date).toLocaleDateString('en-IN', { 
        day: 'numeric', 
        month: 'short', 
        year: 'numeric' 
      });
    } else if (timeframe === 'monthly') {
      return new Date(dataPoint.month + '-01').toLocaleDateString('en-IN', { 
        month: 'long', 
        year: 'numeric' 
      });
    } else {
      return dataPoint.year;
    }
  };
  return (
    <Grid container spacing={{ xs: 2, md: 3 }}>
      {/* Left Side - Current Holdings */}
      <Grid size={{xs:12, md:6}}>
        <Card sx={{ 
          background: 'linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%)', 
          color: 'white',
          borderRadius: { xs: 2, md: 3 },
          boxShadow: '0 8px 32px rgba(0,0,0,0.5)',
          overflow: 'hidden',
          position: 'relative',
          border: '1px solid #333',
          height: { xs: 'auto',md: '100%', lg: '100%' },
          minHeight: { xs: 'auto', md: 400, lg: 400 }
        }}>
          <Box sx={{ 
            position: 'absolute', 
            top: 0, 
            right: 0, 
            width: { xs: '150px', md: '200px' }, 
            height: { xs: '150px', md: '200px' }, 
            background: 'radial-gradient(circle, rgba(255,255,255,0.05) 0%, transparent 70%)',
            transform: 'translate(50px, -50px)'
          }} />
          <CardContent sx={{ p: { xs: 2, md: 4 }, position: 'relative', zIndex: 1, height: '100%' }}>
            <Typography variant="h5" sx={{ 
              fontWeight: 600, 
              mb: 1, 
              opacity: 0.9, 
              textTransform: 'uppercase', 
              letterSpacing: 1,
              fontSize: { xs: '1rem', md: '1.25rem' }
            }}>
              Current Holdings (INR)
            </Typography>
            
            <Box sx={{ 
              display: 'flex', 
              alignItems: 'center', 
              justifyContent: 'space-between',
              mb: 3 
            }}>
              <Typography variant="h2" sx={{ 
                fontWeight: 700, 
                fontSize: { xs: '2rem', sm: '2.5rem', md: '3rem' }
              }}>
                {formatCompactCurrency(portfolioSummary.totalValue)}
              </Typography>
              <IconButton
                onClick={() => setShowValues(!showValues)}
                sx={{ 
                  color: 'white', 
                  bgcolor: 'rgba(255,255,255,0.1)', 
                  '&:hover': { bgcolor: 'rgba(255,255,255,0.2)' },
                  size: { xs: 'small', md: 'medium' }
                }}
              >
                {showValues ? <Visibility /> : <VisibilityOff />}
              </IconButton>
            </Box>

            {/* Performance Chart Area */}
            <Box sx={{ 
              height: { xs: selectedDataPoint ? 100 : 120, md: 150 }, 
              mb: 3,
              position: 'relative',
              bgcolor: 'rgba(255,255,255,0.03)',
              borderRadius: 2,
              p: 2,
              transition: 'height 0.3s ease'
            }}>
              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'space-between', 
                alignItems: 'center',
                mb: 1
              }}>
                <FormControl size="small" sx={{ minWidth: 120 }}>
                  <Select
                    value={timeframe}
                    onChange={(e) => {
                      setTimeframe(e.target.value);
                      setSelectedDataPoint(null); // Clear selection when timeframe changes
                    }}
                    displayEmpty
                    sx={{
                      color: 'white',
                      bgcolor: 'rgba(255,255,255,0.08)',
                      borderRadius: 2,
                      '& .MuiOutlinedInput-notchedOutline': {
                        borderColor: 'rgba(255,255,255,0.2)',
                        borderWidth: '1px',
                      },
                      '&:hover .MuiOutlinedInput-notchedOutline': {
                        borderColor: 'rgba(255,255,255,0.4)',
                      },
                      '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                        borderColor: 'rgba(255,255,255,0.6)',
                        borderWidth: '2px',
                      },
                      '& .MuiSvgIcon-root': {
                        color: 'white',
                      },
                      fontSize: '0.875rem',
                      fontWeight: 500,
                      height: 36,
                      '& .MuiSelect-select': {
                        padding: '8px 12px',
                        display: 'flex',
                        alignItems: 'center',
                        gap: 1,
                      },
                      '&:hover': {
                        bgcolor: 'rgba(255,255,255,0.12)',
                      },
                      transition: 'all 0.2s ease',
                    }}
                    MenuProps={{
                      PaperProps: {
                        sx: {
                          bgcolor: '#1a1a1a',
                          border: '1px solid rgba(255,255,255,0.2)',
                          borderRadius: 2,
                          mt: 1,
                          '& .MuiMenuItem-root': {
                            color: 'white',
                            fontSize: '0.875rem',
                            '&:hover': {
                              bgcolor: 'rgba(255,255,255,0.1)',
                            },
                            '&.Mui-selected': {
                              bgcolor: 'rgba(255,255,255,0.15)',
                              '&:hover': {
                                bgcolor: 'rgba(255,255,255,0.2)',
                              },
                            },
                          },
                        },
                      },
                    }}
                  >
                    <MenuItem value="daily" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Box sx={{ width: 8, height: 8, borderRadius: '50%', bgcolor: '#4caf50' }} />
                      Daily
                    </MenuItem>
                    <MenuItem value="monthly" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Box sx={{ width: 8, height: 8, borderRadius: '50%', bgcolor: '#ff9800' }} />
                      Monthly
                    </MenuItem>
                    <MenuItem value="yearly" sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                      <Box sx={{ width: 8, height: 8, borderRadius: '50%', bgcolor: '#2196f3' }} />
                      Yearly
                    </MenuItem>
                  </Select>
                </FormControl>
                {performanceData.length > 0 && (
                  <Chip
                    icon={performanceData[performanceData.length - 1]?.change >= 0 ? <TrendingUp /> : <TrendingDown />}
                    label={`${formatCurrency(Math.abs(performanceData[performanceData.length - 1]?.change || 0))} (${formatPercentage(((performanceData[performanceData.length - 1]?.change || 0) / (performanceData[performanceData.length - 2]?.value || 1)) * 100)})`}
                    sx={{
                      bgcolor: (performanceData[performanceData.length - 1]?.change || 0) >= 0 ? 'rgba(76, 175, 80, 0.2)' : 'rgba(244, 67, 54, 0.2)',
                      color: 'white',
                      border: '1px solid rgba(255,255,255,0.3)',
                      fontWeight: 600,
                      fontSize: { xs: '0.75rem', md: '0.875rem' },
                    }}
                  />
                )}

              </Box>
              
              {/* Performance Chart */}
              <Box sx={{ 
                height: 'calc(100% - 40px)',
                display: 'flex',
                alignItems: 'flex-end',
                gap: 1,
                px: 1,
                minHeight: { xs: selectedDataPoint ? 60 : 80, md: 110 }
              }}>
                {performanceData.length > 0 ? (
                  (() => {
                    // Limit to maximum 12 bars for better visualization
                    const maxBars = 12;
                    let displayData = performanceData;
                    
                    // If we have more data points than maxBars, sample the data intelligently
                    if (performanceData.length > maxBars) {
                      displayData = [];
                      
                      // Always include the first and last data points
                      displayData.push(performanceData[0]);
                      
                      // Calculate step size for remaining data points
                      const remainingSlots = maxBars - 2; // -2 for first and last
                      const step = (performanceData.length - 2) / remainingSlots;
                      
                      // Add evenly distributed middle points
                      for (let i = 1; i <= remainingSlots; i++) {
                        const index = Math.round(1 + (i * step));
                        if (index < performanceData.length - 1) {
                          displayData.push(performanceData[index]);
                        }
                      }
                      
                      // Always include the last data point
                      displayData.push(performanceData[performanceData.length - 1]);
                    }
                    
                    const maxValue = Math.max(...displayData.map(d => d.value));
                    const minValue = Math.min(...displayData.map(d => d.value));
                    const range = maxValue - minValue;
                    
                    return displayData.map((dataPoint, index) => {
                      const height = range > 0 ? ((dataPoint.value - minValue) / range) * 100 : 50;
                      const isSelected = selectedDataPoint?.index === index;
                      
                      return (
                        <Box
                          key={index}
                          sx={{
                            width: `${100 / displayData.length}%`,
                            height: `${height}%`,
                            bgcolor: isSelected 
                              ? (dataPoint.change >= 0 ? 'rgba(76, 175, 80, 0.9)' : 'rgba(244, 67, 54, 0.9)')
                              : (dataPoint.change >= 0 ? 'rgba(76, 175, 80, 0.6)' : 'rgba(244, 67, 54, 0.6)'),
                            borderRadius: '2px 2px 0 0',
                            position: 'relative',
                            transition: 'all 0.3s ease',
                            cursor: 'pointer',
                            boxShadow: isSelected 
                              ? (dataPoint.change >= 0 
                                  ? '0 0 8px rgba(76, 175, 80, 0.6)' 
                                  : '0 0 8px rgba(244, 67, 54, 0.6)')
                              : 'none',
                            '&:hover': {
                              bgcolor: dataPoint.change >= 0 ? 'rgba(76, 175, 80, 0.8)' : 'rgba(244, 67, 54, 0.8)',
                              transform: 'scaleY(1.05)',
                            },
                            '&:active': {
                              transform: 'scaleY(0.95)',
                            }
                          }}
                          title={`${timeframe === 'daily' ? dataPoint.date : timeframe === 'monthly' ? dataPoint.month : dataPoint.year}: ${formatCurrency(dataPoint.value)} (${dataPoint.change >= 0 ? '+' : ''}${formatCurrency(dataPoint.change)})`}
                          onClick={() => handleDataPointSelect(dataPoint, index)}
                          onTouchStart={() => handleDataPointSelect(dataPoint, index)}
                        />
                      );
                    });
                  })()
                ) : (
                  // Fallback to static bars if no data
                  [20, 35, 25, 45, 30, 40, 35, 50, 45, 60, 55, 70].map((height, index) => (
                    <Box
                      key={index}
                      sx={{
                        width: '8%',
                        height: `${height}%`,
                        bgcolor: 'rgba(255,255,255,0.3)',
                        borderRadius: '2px 2px 0 0',
                        position: 'relative',
                        '&:hover': {
                          bgcolor: 'rgba(255,255,255,0.5)',
                        }
                      }}
                    />
                  ))
                )}
              </Box>
              
              {/* Mobile instruction - below chart */}
              <Typography 
                variant="caption" 
                sx={{ 
                  textAlign: 'center',
                  color: '#aaa',
                  fontSize: '0.75rem',
                  display: { xs: 'block', md: 'none' },
                  mt: 1,
                  opacity: 0.7
                }}
              >
                Tap bars for details
              </Typography>

            </Box>

            {/* Mobile Data Point Details */}
            {selectedDataPoint && (
              <Box sx={{ 
                display: { xs: 'block', md: 'none' },
                mb: 3,
                p: 2,
                bgcolor: 'rgba(255,255,255,0.05)',
                borderRadius: 2,
                border: '1px solid rgba(255,255,255,0.1)',
                position: 'relative'
              }}>
                <IconButton
                  onClick={() => setSelectedDataPoint(null)}
                  sx={{
                    position: 'absolute',
                    top: 8,
                    right: 8,
                    color: '#aaa',
                    size: 'small',
                    '&:hover': {
                      color: 'white',
                      bgcolor: 'rgba(255,255,255,0.1)'
                    }
                  }}
                >
                  <Close fontSize="small" />
                </IconButton>
                <Typography variant="body2" sx={{ 
                  color: '#aaa', 
                  mb: 1,
                  fontSize: '0.875rem',
                  pr: 3 // Add padding to prevent overlap with close button
                }}>
                  {formatDataPointDate(selectedDataPoint)}
                </Typography>
                <Box sx={{ 
                  display: 'flex', 
                  justifyContent: 'space-between',
                  alignItems: 'center'
                }}>
                  <Box>
                    <Typography variant="body2" sx={{ color: '#aaa', mb: 0.5 }}>
                      Portfolio Value
                    </Typography>
                    <Typography variant="h6" sx={{ 
                      fontWeight: 600,
                      fontSize: '1rem'
                    }}>
                      {formatCurrency(selectedDataPoint.value)}
                    </Typography>
                  </Box>
                  <Box sx={{ textAlign: 'right' }}>
                    <Typography variant="body2" sx={{ color: '#aaa', mb: 0.5 }}>
                      Change
                    </Typography>
                    <Typography variant="h6" sx={{ 
                      fontWeight: 600,
                      color: selectedDataPoint.change >= 0 ? '#4caf50' : '#f44336',
                      fontSize: '1rem'
                    }}>
                      {selectedDataPoint.change >= 0 ? '+' : ''}{formatCurrency(selectedDataPoint.change)}
                    </Typography>
                  </Box>
                </Box>
              </Box>
            )}

            {/* Summary Stats */}
            <Box sx={{ 
              display: 'flex', 
              justifyContent: 'space-between',
              alignItems: 'center',
              mb: 3
            }}>
              <Box>
                <Typography variant="body2" sx={{ color: '#aaa', mb: 0.5 }}>
                  Total Returns
                </Typography>
                <Typography variant="h6" sx={{ 
                  fontWeight: 600,
                  color: portfolioSummary.returnPercentage >= 0 ? '#4caf50' : '#f44336'
                }}>
                  {formatCurrency(portfolioSummary.totalReturns)} ({formatPercentage(portfolioSummary.returnPercentage)})
                </Typography>
              </Box>
              <Box sx={{ textAlign: 'right' }}>
                <Typography variant="body2" sx={{ color: '#aaa', mb: 0.5 }}>
                  Invested Amount
                </Typography>
                <Typography variant="h6" sx={{ fontWeight: 600 }}>
                  {formatCompactCurrency(portfolioSummary.totalInvested)}
                </Typography>
              </Box>
            </Box>
          </CardContent>
        </Card>
      </Grid>

      {/* Right Side - Asset Allocation */}
      <Grid size={{xs:12, md:6}}>
        <Card sx={{ 
          background: 'linear-gradient(135deg, #0a0a0a 0%, #1a1a1a 100%)', 
          color: 'white',
          borderRadius: { xs: 2, md: 3 },
          boxShadow: '0 8px 32px rgba(0,0,0,0.5)',
          overflow: 'hidden',
          position: 'relative',
          border: '1px solid #333',
          height: { xs: 'auto', md: '100%', lg: '100%' },
          minHeight: { xs: 'auto', md: 400, lg: 400 }
        }}>
          <Box sx={{ 
            position: 'absolute', 
            top: 0, 
            left: 0, 
            width: { xs: '150px', md: '200px' }, 
            height: { xs: '150px', md: '200px' }, 
            background: 'radial-gradient(circle, rgba(255,255,255,0.05) 0%, transparent 70%)',
            transform: 'translate(-50px, -50px)'
          }} />
          <CardContent sx={{ p: { xs: 2, md: 4 }, position: 'relative', zIndex: 1, height: '100%' }}>
            <Typography variant="h5" sx={{ 
              fontWeight: 600, 
              mb: 3, 
              opacity: 0.9, 
              textTransform: 'uppercase', 
              letterSpacing: 1,
              fontSize: { xs: '1rem', md: '1.25rem' }
            }}>
              Asset Allocation
            </Typography>
            


            {/* Asset Categories Legend */}
            <Box sx={{ 
              display: { xs: 'flex', md: 'grid' },
              flexDirection: { xs: 'column', md: 'row' },
              gridTemplateColumns: { md: '1fr 1fr' },
              gap: { xs: 1.5, md: 2 },
              mt: { xs: 3, md: 2 },
              alignItems: { md: 'center' },
              minHeight: { md: 300 }
            }}>
              {/* Pie Chart */}
              <Box sx={{ 
                display: 'flex', 
                justifyContent: 'center', 
                alignItems: 'center',
                height: { xs: 200, md: 250 },
                mb: { xs: 2, md: 0 }
              }}>
                <svg
                  width="240"
                  height="240"
                  viewBox="0 0 240 240"
                  style={{ overflow: 'visible' }}
                >
                  <defs>
                    <filter id="glow" x="-50%" y="-50%" width="200%" height="200%">
                      <feGaussianBlur stdDeviation="3" result="coloredBlur"/>
                      <feMerge> 
                        <feMergeNode in="coloredBlur"/>
                        <feMergeNode in="SourceGraphic"/>
                      </feMerge>
                    </filter>
                  </defs>
                  
                  {/* Pie Chart Segments */}
                  <g transform="translate(120, 120)">
                    {/* Calculate angles based on percentages */}
                    {(() => {
                      const data = assetAllocation?.chartData || [
                        { percentage: 45.7, color: '#4caf50', name: 'Mutual Funds' },
                        { percentage: 29.2, color: '#ff9800', name: 'Fixed Deposits' },
                        { percentage: 16.8, color: '#9c27b0', name: 'PPF' },
                        { percentage: 7.4, color: '#2196f3', name: 'Stocks' },
                        { percentage: 0.8, color: '#9e9e9e', name: 'Others' }
                      ];
                      
                      const radius = 120;
                      const innerRadius = 60;
                      let currentAngle = -90; // Start from top
                      
                      return data.map((item, index) => {
                        const angle = (item.percentage / 100) * 360;
                        const startAngle = currentAngle;
                        const endAngle = currentAngle + angle;
                        
                        // Convert angles to radians
                        const startRad = (startAngle * Math.PI) / 180;
                        const endRad = (endAngle * Math.PI) / 180;
                        
                        // Calculate arc coordinates
                        const x1 = radius * Math.cos(startRad);
                        const y1 = radius * Math.sin(startRad);
                        const x2 = radius * Math.cos(endRad);
                        const y2 = radius * Math.sin(endRad);
                        
                        // Determine if arc is large
                        const largeArcFlag = angle > 180 ? 1 : 0;
                        
                        // Create path for the arc
                        const pathData = [
                          `M ${x1} ${y1}`,
                          `A ${radius} ${radius} 0 ${largeArcFlag} 1 ${x2} ${y2}`,
                          `L ${x2 * (innerRadius / radius)} ${y2 * (innerRadius / radius)}`,
                          `A ${innerRadius} ${innerRadius} 0 ${largeArcFlag} 0 ${x1 * (innerRadius / radius)} ${y1 * (innerRadius / radius)}`,
                          'Z'
                        ].join(' ');
                        
                        currentAngle = endAngle;
                        
                        return (
                          <path
                            key={index}
                            d={pathData}
                            fill={item.color}
                            stroke="#1a1a1a"
                            strokeWidth="2"
                            style={{ 
                              cursor: 'pointer',
                              transition: 'all 0.3s ease',
                              transformOrigin: 'center'
                            }}
                            onMouseEnter={(e) => {
                              e.target.style.filter = "url(#glow)";
                              e.target.style.transform = "scale(1.02)";
                            }}
                            onMouseLeave={(e) => {
                              e.target.style.filter = "none";
                              e.target.style.transform = "scale(1)";
                            }}
                          />
                        );
                      });
                    })()}
                  </g>
                  
                  {/* Center circle */}
                  <circle
                    cx="120"
                    cy="120"
                    r="40"
                    fill="#1a1a1a"
                    stroke="#333"
                    strokeWidth="2"
                  />
                  
                  {/* Center text */}
                  <text
                    x="120"
                    y="115"
                    textAnchor="middle"
                    fill="white"
                    fontSize="12"
                    fontWeight="600"
                  >
                    Total
                  </text>
                  <text
                    x="120"
                    y="130"
                    textAnchor="middle"
                    fill="#aaa"
                    fontSize="10"
                  >
                    {assetAllocation?.totalPercentage || 100}%
                  </text>
                </svg>
              </Box>

              {/* Legend - Right side on desktop, below on mobile */}
              <Box sx={{ 
                display: 'flex', 
                flexDirection: 'column',
                gap: 1.5,
                justifyContent: { md: 'center' },
                height: { md: '100%' }
              }}>
                {(assetAllocation?.chartData || [
                  { color: '#4caf50', label: 'Mutual Funds', value: '45.7%' },
                  { color: '#ff9800', label: 'Fixed Deposits', value: '29.2%' },
                  { color: '#9c27b0', label: 'PPF', value: '16.8%' },
                  { color: '#2196f3', label: 'Stocks', value: '7.4%' },
                  { color: '#9e9e9e', label: 'Others', value: '0.8%' },
                ]).map((asset, index) => (
                  <Box key={index} sx={{ 
                    display: 'flex', 
                    alignItems: 'center', 
                    gap: 2,
                    p: 1,
                    borderRadius: 1,
                    '&:hover': {
                      bgcolor: 'rgba(255,255,255,0.05)',
                    }
                  }}>
                    <Box sx={{ 
                      width: 16, 
                      height: 16, 
                      borderRadius: '50%', 
                      bgcolor: asset.color,
                      flexShrink: 0
                    }} />
                    <Box sx={{ flex: 1 }}>
                      <Typography variant="body2" sx={{ 
                        color: 'white', 
                        fontWeight: 500,
                        fontSize: '0.875rem'
                      }}>
                        {asset.name || asset.label}
                      </Typography>
                    </Box>
                    <Typography variant="body2" sx={{ 
                      fontWeight: 600,
                      color: 'white',
                      fontSize: '0.875rem'
                    }}>
                      {asset.percentage ? `${asset.percentage}%` : asset.value}
                    </Typography>
                  </Box>
                ))}
              </Box>
            </Box>
          </CardContent>
        </Card>
      </Grid>
    </Grid>
  );
};

export default PortfolioSummary; 