import React from "react";
import { Box, Paper, Typography } from "@mui/material";
import { TrendingUp, TrendingDown } from "@mui/icons-material";

const PhoneMockup = () => {
  // Generate sample stock data points for the graph
  const generateStockData = () => {
    const data = [];
    let value = 100;
    for (let i = 0; i < 20; i++) {
      value += (Math.random() - 0.5) * 10;
      data.push(value);
    }
    return data;
  };

  const stockData = generateStockData();
  const currentValue = stockData[stockData.length - 1];
  const previousValue = stockData[stockData.length - 2];
  const isPositive = currentValue > previousValue;

  return (
    <Box
      sx={{
        position: "relative",
        display: "flex",
        justifyContent: { xs: "center", md: "flex-end" },
        alignItems: "center",
        height: "400px",
        pr: { xs: 0, md: 2 },
        pt: { xs: 6, md: 0 },
      }}
    >
      {/* Phone Frame */}
      <Paper
        elevation={12}
        sx={{
          width: { xs: 280, md: 340 },
          height: { xs: 560, md: 620 },
          borderRadius: 4,
          background: "linear-gradient(145deg, #2a2a2a, #1a1a1a)",
          border: "8px solid #333",
          position: "relative",
          overflow: "hidden",
          "&::before": {
            content: '""',
            position: "absolute",
            top: 8,
            left: "50%",
            transform: "translateX(-50%)",
            width: 60,
            height: 4,
            borderRadius: 2,
            backgroundColor: "#666",
            zIndex: 2,
          },
        }}
      >
        {/* Phone Screen Content */}
        <Box
          sx={{
            width: "100%",
            height: "100%",
            background: "linear-gradient(135deg, #1e1e1e 0%, #2d2d2d 100%)",
            padding: 2,
            display: "flex",
            flexDirection: "column",
            overflow: "hidden",
            boxSizing: "border-box",
          }}
        >
          {/* Header */}
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              alignItems: "center",
              mb: 2,
              flexShrink: 0,
            }}
          >
            <Typography
              variant="h6"
              sx={{
                color: "#f0a500",
                fontWeight: 600,
                fontSize: { xs: "0.875rem", md: "1rem" },
                lineHeight: 1,
              }}
            >
              My Portfolio
            </Typography>
            <Box sx={{ display: "flex", alignItems: "center" }}>
              {isPositive ? (
                <TrendingUp sx={{ color: "#4caf50", fontSize: 18, mr: 0.5 }} />
              ) : (
                <TrendingDown
                  sx={{ color: "#f44336", fontSize: 18, mr: 0.5 }}
                />
              )}
              <Typography
                variant="body2"
                sx={{
                  color: isPositive ? "#4caf50" : "#f44336",
                  fontWeight: 600,
                  fontSize: "0.75rem",
                  lineHeight: 1,
                }}
              >
                {isPositive ? "+" : ""}
                {(
                  ((currentValue - previousValue) / previousValue) *
                  100
                ).toFixed(2)}
                %
              </Typography>
            </Box>
          </Box>

          {/* Total Value */}
          <Box sx={{ mb: 3, flexShrink: 0 }}>
            <Typography
              variant="h4"
              sx={{
                color: "white",
                fontWeight: 700,
                mb: 0.5,
                fontSize: { xs: "1.75rem", md: "2rem" },
                lineHeight: 1,
              }}
            >
              {currentValue.toFixed(0)}K
            </Typography>
            <Typography
              variant="body2"
              sx={{
                color: "#ccc",
                fontSize: "0.875rem",
                lineHeight: 1,
              }}
            >
              Total Portfolio Value
            </Typography>
          </Box>

          {/* Stock Chart */}
          <Box
            sx={{
              flex: 1,
              mb: 2,
              mr: 2,
              minHeight: 0,
              display: "flex",
              flexDirection: "column",
            }}
          >
            <Box
              sx={{
                width: "100%",
                height: "100%",
                position: "relative",
                background: "rgba(255, 255, 255, 0.05)",
                borderRadius: 2,
                p: 1,
                overflow: "hidden",
                flex: 1,
              }}
            >
              {/* Chart Line */}
              <svg
                width="100%"
                height="100%"
                viewBox="0 0 240 120"
                style={{ position: "absolute", top: 0, left: 0 }}
                preserveAspectRatio="xMidYMid meet"
              >
                <defs>
                  <linearGradient
                    id="chartGradient"
                    x1="0%"
                    y1="0%"
                    x2="0%"
                    y2="100%"
                  >
                    <stop offset="0%" stopColor="#f0a500" stopOpacity="0.8" />
                    <stop offset="100%" stopColor="#f0a500" stopOpacity="0.1" />
                  </linearGradient>
                </defs>

                {/* Area under the curve */}
                <path
                  d={`M 0 ${120 - (stockData[0] - 90) * 2} ${stockData
                    .map(
                      (value, index) =>
                        `L ${(index + 1) * 12} ${120 - (value - 90) * 2}`
                    )
                    .join(" ")} L 240 120 L 0 120 Z`}
                  fill="url(#chartGradient)"
                />

                {/* Line */}
                <path
                  d={`M 0 ${120 - (stockData[0] - 90) * 2} ${stockData
                    .map(
                      (value, index) =>
                        `L ${(index + 1) * 12} ${120 - (value - 90) * 2}`
                    )
                    .join(" ")}`}
                  stroke="#f0a500"
                  strokeWidth="2"
                  fill="none"
                />
              </svg>
            </Box>
          </Box>

          {/* Quick Stats */}
          <Box
            sx={{
              display: "flex",
              justifyContent: "space-between",
              flexShrink: 0,
              alignItems: "flex-end",
            }}
          >
            <Box sx={{ textAlign: "center", flex: 1 }}>
              <Typography
                variant="h6"
                sx={{
                  color: "#f0a500",
                  fontWeight: 600,
                  fontSize: { xs: "1rem", md: "1.125rem" },
                  lineHeight: 1,
                  mb: 0.5,
                }}
              >
                8
              </Typography>
              <Typography
                variant="caption"
                sx={{
                  color: "#ccc",
                  fontSize: "0.75rem",
                  lineHeight: 1,
                }}
              >
                Investments
              </Typography>
            </Box>
            <Box sx={{ textAlign: "center", flex: 1 }}>
              <Typography
                variant="h6"
                sx={{
                  color: "#4caf50",
                  fontWeight: 600,
                  fontSize: { xs: "1rem", md: "1.125rem" },
                  lineHeight: 1,
                  mb: 0.5,
                }}
              >
                45K
              </Typography>
              <Typography
                variant="caption"
                sx={{
                  color: "#ccc",
                  fontSize: "0.75rem",
                  lineHeight: 1,
                }}
              >
                This Month
              </Typography>
            </Box>
            <Box sx={{ textAlign: "center", flex: 1 }}>
              <Typography
                variant="h6"
                sx={{
                  color: "#2196f3",
                  fontWeight: 600,
                  fontSize: { xs: "1rem", md: "1.125rem" },
                  lineHeight: 1,
                  mb: 0.5,
                }}
              >
                12.5%
              </Typography>
              <Typography
                variant="caption"
                sx={{
                  color: "#ccc",
                  fontSize: "0.75rem",
                  lineHeight: 1,
                }}
              >
                Returns
              </Typography>
            </Box>
          </Box>
        </Box>
      </Paper>
    </Box>
  );
};

export default PhoneMockup;
