import React, { useState } from "react";
import ForgotPassword from "../components/auth/ForgotPassword";
import {
  resetPassword,
  sendOtp,
  validateOtp,
} from "../services/api/authService";
import { TOAST_MESSAGE_TYPE, TOAST_MESSAGE } from "../constants/common";
import { showToast } from "../utils/toast";
import { FORGOT_PASSWORD } from "../constants/auth/ForgotPasswordConstants";

function ForgotPasswordPage() {
  const [step, setStep] = useState(1); // 1: email, 2: otp, 3: reset
  const [email, setEmail] = useState("");
  const [otp, setOtp] = useState("");
  const [otpTimer, setOtpTimer] = useState(0);
  const [loading, setLoading] = useState(false);
  const [newPassword, setNewPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");

  // Step 1: Request OTP
  const handleRequestOtp = async () => {
    if (!email) {
      showToast(
        TOAST_MESSAGE_TYPE.TYPE_WARNING,
        FORGOT_PASSWORD.EMAIL_REQUIRED
      );
      return;
    }
    setLoading(true);
    try {
      const response = await sendOtp({ emailId: email });
      if (!response.ok) {
        const errorData = await response.json();
        showToast(TOAST_MESSAGE_TYPE.TYPE_WARNING, errorData.errorMessage);
      } else {
        showToast(TOAST_MESSAGE_TYPE.TYPE_SUCCESS, FORGOT_PASSWORD.OTP_SENT);
        const data = await response.json();
        setOtpTimer(data.expireSec || 60);
        setStep(2);
      }
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGE.API_ERROR);
    } finally {
      setLoading(false);
    }
  };

  // Step 2: OTP Validation
  const handleOtpValidated = async (otpValue) => {
    setLoading(true);
    try {
      const response = await validateOtp({
        emailId: email,
        otp: { code: otpValue },
      });
      if (response.ok) {
        setOtp(otpValue);
        setStep(3);
      } else {
        const errorData = await response.json();
        showToast(
          TOAST_MESSAGE_TYPE.TYPE_WARNING,
          errorData.errorMessage || FORGOT_PASSWORD.INVALID_OTP
        );
      }
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGE.API_ERROR);
    } finally {
      setLoading(false);
    }
  };

  // Step 3: Reset Password
  const handleResetPassword = async () => {
    if (!newPassword || !confirmPassword) {
      showToast(
        TOAST_MESSAGE_TYPE.TYPE_WARNING,
        FORGOT_PASSWORD.FILL_ALL_FIELDS
      );
      return;
    }
    if (newPassword !== confirmPassword) {
      showToast(
        TOAST_MESSAGE_TYPE.TYPE_WARNING,
        FORGOT_PASSWORD.PASSWORDS_NOT_MATCH
      );
      return;
    }
    setLoading(true);
    try {
      const response = await resetPassword({
        emailId: email,
        otp: { code: otp },
        newPassword: btoa(newPassword),
      });
      if (!response.ok) {
        const errorData = await response.json();
        showToast(
          TOAST_MESSAGE_TYPE.TYPE_WARNING,
          errorData.errorMessage || FORGOT_PASSWORD.RESET_FAILED
        );
      } else {
        showToast(
          TOAST_MESSAGE_TYPE.TYPE_SUCCESS,
          FORGOT_PASSWORD.RESET_SUCCESS
        );
        setStep(4); // Done
      }
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGE.API_ERROR);
    } finally {
      setLoading(false);
    }
  };

  return (
    <ForgotPassword
      step={step}
      email={email}
      setEmail={setEmail}
      otpTimer={otpTimer}
      setOtpTimer={setOtpTimer}
      loading={loading}
      newPassword={newPassword}
      setNewPassword={setNewPassword}
      confirmPassword={confirmPassword}
      setConfirmPassword={setConfirmPassword}
      handleRequestOtp={handleRequestOtp}
      handleOtpValidated={handleOtpValidated}
      handleResetPassword={handleResetPassword}
    />
  );
}

export default ForgotPasswordPage;
