import React, { useState, useEffect, useCallback, useMemo } from "react";
import { Box, Container, CircularProgress, useMediaQuery } from "@mui/material";
import { DASHBOARD } from "../constants/dashboard";
import {
  getTransactionColor,
  getTransactionIcon,
  formatDate,
  formatCurrency as formatTransactionCurrency,
} from "../utils/transactionUtils";
import userDataService from "../services/api/userDataService";
import { useContext } from "react";
import { AuthContext } from "../services/context/AuthContext";

// Import components
import PortfolioSummary from "../components/dashboard/PortfolioSummary";
import SearchBar from "../components/dashboard/SearchBar";
import CategoriesMobileDropdown from "../components/dashboard/CategoriesMobileDropdown";
import CategoriesWebSidebar from "../components/dashboard/CategoriesWebSidebar";
import DeleteConfirmationDialog from "../components/dashboard/common/DeleteConfirmationDialog";
import TransactionAmendDialogSkeleton from "../components/dashboard/common/TransactionAmendDialogSkeleton";
import TransactionHistory from "../components/dashboard/common/TransactionHistory";

// Import Investment Add/Modify Dialogs
import InvestmentAmendDialogSkeleton from "../components/dashboard/common/InvestmentAmendDialogSkeleton";
import {
  FIXED_DEPOSIT_AMEND_DATA,
  MUTUAL_FUNDS_AMEND_DATA,
  STOCKS_AMEND_DATA,
  PPF_AMEND_DATA,
  EPF_AMEND_DATA,
  NPS_AMEND_DATA,
  BONDS_AMEND_DATA,
  GOLD_AMEND_DATA,
  REAL_ESTATE_AMEND_DATA,
  CRYPTO_AMEND_DATA,
  OTHERS_AMEND_DATA,
} from "../constants/dashboard/common/InvestmentAmendDialogSkeletonConstants";

const DashboardPage = () => {
  const isMobile = useMediaQuery("(max-width: 900px)");
  const { isAuthenticated } = useContext(AuthContext);

  const [portfolioSummary, setPortfolioSummary] = useState(null);
  const [categoryData, setCategoryData] = useState({});
  const [loading, setLoading] = useState(true);
  const [showValues, setShowValues] = useState(true);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [expandedCategory, setExpandedCategory] = useState("");
  const [addDialogOpen, setAddDialogOpen] = useState(false);
  const [editDialogOpen, setEditDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedInvestment, setSelectedInvestment] = useState(null);
  const [searchQuery, setSearchQuery] = useState("");
  const [debouncedSearchQuery, setDebouncedSearchQuery] = useState("");

  const [formData, setFormData] = useState({
    name: "",
    currentValue: "",
    investedAmount: "",
    description: "",
  });

  // Transaction History States
  const [showTransactionHistory, setShowTransactionHistory] = useState(false);
  const [transactionHistory, setTransactionHistory] = useState([]);
  const [transactionMenuAnchor, setTransactionMenuAnchor] = useState(null);
  const [transactionMenuId, setTransactionMenuId] = useState(null);

  const [selectedTransaction, setSelectedTransaction] = useState(null);
  const [transactionDialogOpen, setTransactionDialogOpen] = useState(false);
  const [transactionDialogType, setTransactionDialogType] = useState("add");
  const [transactionFormData, setTransactionFormData] = useState({
    transactionType: "BUY",
    amount: "",
    quantity: "",
    transactionDate: new Date(),
    description: "",
  });

  // Add state
  const [deleteTransactionDialogOpen, setDeleteTransactionDialogOpen] =
    useState(false);

  useEffect(() => {
    if (isAuthenticated && userDataService.data) {
      loadDashboardData();
    }
  }, [isAuthenticated]);

  // Debounce search query
  useEffect(() => {
    const timer = setTimeout(() => {
      setDebouncedSearchQuery(searchQuery);
    }, 300);

    return () => clearTimeout(timer);
  }, [searchQuery]);

  const loadDashboardData = async () => {
    try {
      setLoading(true);
      const summary = userDataService.getPortfolioSummary();
      const categories = userDataService.getCategories();

      setPortfolioSummary(summary);
      setCategoryData(categories);

      // Set first category with investments as default selected
      const firstCategoryWithInvestments = Object.keys(categories).find(
        (key) => categories[key].totalValue > 0
      );
      setSelectedCategory(
        firstCategoryWithInvestments || Object.keys(categories)[0]
      );
    } catch (error) {
      console.error("Error loading dashboard data:", error);
    } finally {
      setLoading(false);
    }
  };

  const handleAddInvestment = (category) => {
    setSelectedCategory(category);
    setFormData({
      name: "",
      currentValue: "",
      investedAmount: "",
      description: "",
    });
    setAddDialogOpen(true);
  };

  const handleAccordionChange = (categoryKey) => (event, isExpanded) => {
    setExpandedCategory(isExpanded ? categoryKey : "");
    if (isExpanded) {
      setSelectedCategory(categoryKey);
    }
  };

  const handleEditInvestment = (investment) => {
    if (!investment) {
      console.error("Investment is null or undefined");
      return;
    }

    setSelectedInvestment(investment);
    setFormData({
      name: investment.name,
      currentValue: investment.currentValue.toString(),
      investedAmount: investment.investedAmount.toString(),
      description: investment.description || "",
    });
    setEditDialogOpen(true);
  };

  const handleDeleteInvestment = (investment) => {
    if (!investment) {
      console.error("Investment is null or undefined");
      return;
    }

    setSelectedInvestment(investment);
    setDeleteDialogOpen(true);
  };

  // Transaction History Handlers
  const handleViewTransactionHistory = (investment) => {
    setSelectedInvestment(investment);
    setTransactionHistory(userDataService.getTransactionHistory(investment.id));
    setShowTransactionHistory(true);
  };

  const handleViewTransactionHistoryForTab = (investment) => {
    setSelectedInvestment(investment);
    setTransactionHistory(userDataService.getTransactionHistory(investment.id));
    // Don't set showTransactionHistory to true to avoid opening popup
  };

  // Reset transaction history when category changes
  const handleCategoryChange = (newCategory) => {
    setSelectedCategory(newCategory);
    setShowTransactionHistory(false);
    setSelectedInvestment(null);
    setTransactionHistory([]);
  };

  const handleAddTransaction = (investment) => {
    setSelectedInvestment(investment);
    setTransactionFormData({
      transactionType: "BUY",
      amount: "",
      quantity: "",
      transactionDate: new Date(),
      description: "",
    });
    setTransactionDialogType("add");
    setTransactionDialogOpen(true);
  };

  const handleTransactionMenuOpen = (event, transactionId) => {
    setTransactionMenuAnchor(event.currentTarget);
    setTransactionMenuId(transactionId);
    const transaction = transactionHistory.find((t) => t.id === transactionId);
    setSelectedTransaction(transaction);
  };

  const handleTransactionMenuClose = () => {
    setTransactionMenuAnchor(null);
    setTransactionMenuId(null);
    setSelectedTransaction(null);
  };

  const handleEditTransaction = (transaction) => {
    setSelectedTransaction(transaction);
    setTransactionFormData({
      transactionType: transaction.transactionType,
      amount: transaction.amount.toString(),
      quantity: transaction.quantity?.toString() || "",
      transactionDate: new Date(transaction.transactionDate),
      description: transaction.description || "",
    });
    setTransactionDialogType("edit");
    setTransactionDialogOpen(true);
    handleTransactionMenuClose();
  };

  const handleDeleteTransaction = (transaction) => {
    setSelectedTransaction(transaction);
    setDeleteTransactionDialogOpen(true);
  };

  const handleConfirmDeleteTransaction = () => {
    // TODO: Call your delete API here
    setDeleteTransactionDialogOpen(false);
    if (selectedInvestment) {
      setTransactionHistory(
        userDataService.getTransactionHistory(selectedInvestment.id)
      );
    }
  };

  const handleSaveTransaction = (formData) => {
    // Here you would call the actual API with formData
    console.log("Saving transaction:", formData);

    if (transactionDialogType === "edit" && selectedTransaction) {
      // Update existing transaction
      console.log("Updating transaction:", selectedTransaction.id, formData);
    } else {
      // Add new transaction
      console.log(
        "Adding new transaction for investment:",
        selectedInvestment?.id,
        formData
      );
    }

    setTransactionDialogOpen(false);
    // Reload transaction history after adding/editing
    if (selectedInvestment) {
      setTransactionHistory(
        userDataService.getTransactionHistory(selectedInvestment.id)
      );
    }
  };

  const handleCloseTransactionHistory = () => {
    setShowTransactionHistory(false);
    setSelectedInvestment(null);
    setTransactionHistory([]);
  };

  const handleSaveEdit = () => {
    // Here you would call the actual API to update the investment
    setEditDialogOpen(false);
    // Reload data after updating
    loadDashboardData();
  };

  const handleConfirmDelete = () => {
    // Here you would call the actual API to delete the investment
    setDeleteDialogOpen(false);
    // Reload data after deleting
    loadDashboardData();
  };

  const handleSaveInvestment = () => {
    // Here you would call the actual API
    setAddDialogOpen(false);
    // Reload data after adding
    loadDashboardData();
  };

  const formatCurrency = useCallback(
    (amount) => {
      if (!showValues) return "";
      return new Intl.NumberFormat("en-IN", {
        style: "currency",
        currency: "INR",
        minimumFractionDigits: 0,
        maximumFractionDigits: 0,
      }).format(amount);
    },
    [showValues]
  );

  const formatPercentage = useCallback(
    (percentage) => {
      if (!showValues) return "";
      return `${percentage > 0 ? "+" : ""}${percentage.toFixed(2)}%`;
    },
    [showValues]
  );

  const formatCompactCurrency = useCallback(
    (amount) => {
      if (!showValues) return "";
      if (amount >= 10000000) {
        return `${(amount / 10000000).toFixed(1)}Cr`;
      } else if (amount >= 100000) {
        return `${(amount / 100000).toFixed(1)}L`;
      } else if (amount >= 1000) {
        return `${(amount / 1000).toFixed(0)}K`;
      }
      return formatCurrency(amount);
    },
    [showValues, formatCurrency]
  );

  // Search functionality
  const getFilteredCategoryData = useMemo(() => {
    if (!debouncedSearchQuery.trim()) {
      return categoryData;
    }

    return userDataService.searchData(debouncedSearchQuery);
  }, [debouncedSearchQuery, categoryData]);

  const filteredCategoryData = getFilteredCategoryData;
  const hasSearchResults = Object.keys(filteredCategoryData).length > 0;

  // Auto-expand first category when searching
  useEffect(() => {
    if (debouncedSearchQuery.trim() && hasSearchResults) {
      const firstFilteredCategory = Object.keys(filteredCategoryData)[0];
      if (firstFilteredCategory && firstFilteredCategory !== expandedCategory) {
        setExpandedCategory(firstFilteredCategory);
        setSelectedCategory(firstFilteredCategory);
      }
    }
    // Removed the else clause that was clearing expandedCategory
  }, [
    debouncedSearchQuery,
    hasSearchResults,
    expandedCategory,
    filteredCategoryData,
  ]);

  if (loading) {
    return (
      <Box
        sx={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",
          minHeight: "100vh",
        }}
      >
        <CircularProgress size={60} />
      </Box>
    );
  }

  const selectedCategoryInfo =
    DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory];

  // Investment Add/Modify dialog mapping for different categories
  const categoryAmendConfigs = {
    FIXED_DEPOSITS: FIXED_DEPOSIT_AMEND_DATA,
    MUTUAL_FUNDS: MUTUAL_FUNDS_AMEND_DATA,
    STOCKS: STOCKS_AMEND_DATA,
    PPF: PPF_AMEND_DATA,
    EPF: EPF_AMEND_DATA,
    NPS: NPS_AMEND_DATA,
    BONDS: BONDS_AMEND_DATA,
    GOLD: GOLD_AMEND_DATA,
    REAL_ESTATE: REAL_ESTATE_AMEND_DATA,
    CRYPTO: CRYPTO_AMEND_DATA,
    OTHERS: OTHERS_AMEND_DATA,
  };

  return (
    <Box sx={{ pt: 14, pb: 4, minHeight: "100vh", bgcolor: "#0a0a0a" }}>
      <Container maxWidth={false} sx={{ px: { xs: 2, sm: 3, md: 4 } }}>
        {/* Portfolio Summary Section */}
        <Box sx={{ mb: 4 }}>
          <PortfolioSummary
            portfolioSummary={portfolioSummary}
            showValues={showValues}
            setShowValues={setShowValues}
            formatCompactCurrency={formatCompactCurrency}
            formatCurrency={formatCurrency}
            formatPercentage={formatPercentage}
            assetAllocation={userDataService.getAssetAllocation()}
            performanceHistory={userDataService.getPerformanceHistory()}
          />
        </Box>

        {/* Search Bar Section */}
        <Box sx={{ mb: 4 }}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              gap: 3,
            }}
          >
            <SearchBar
              searchQuery={searchQuery}
              setSearchQuery={setSearchQuery}
            />
          </Box>
        </Box>

        {/* Main Content - Mobile Accordion vs Desktop Sidebar */}
        {isMobile ? (
          <>
            <CategoriesMobileDropdown
              filteredCategoryData={filteredCategoryData}
              expandedCategory={expandedCategory}
              handleAccordionChange={handleAccordionChange}
              handleAddInvestment={handleAddInvestment}
              handleEditInvestment={handleEditInvestment}
              handleDeleteInvestment={handleDeleteInvestment}
              handleViewTransactionHistory={handleViewTransactionHistory}
              formatCompactCurrency={formatCompactCurrency}
              formatCurrency={formatCurrency}
              formatPercentage={formatPercentage}
              searchQuery={searchQuery}
              hasSearchResults={hasSearchResults}
              showValues={showValues}
              // Transaction history props
              transactionHistory={transactionHistory}
              showTransactionHistory={showTransactionHistory}
              selectedInvestment={selectedInvestment}
              handleAddTransaction={handleAddTransaction}
              handleEditTransaction={handleEditTransaction}
              handleDeleteTransaction={handleDeleteTransaction}
              handleCloseTransactionHistory={handleCloseTransactionHistory}
              transactionMenuAnchor={transactionMenuAnchor}
              transactionMenuId={transactionMenuId}
              handleTransactionMenuOpen={handleTransactionMenuOpen}
              handleTransactionMenuClose={handleTransactionMenuClose}
            />
          </>
        ) : (
          <Box
            sx={{
              display: "flex",
              gap: 3,
              width: "100%",
              minHeight: 0,
            }}
          >
            <CategoriesWebSidebar
              filteredCategoryData={filteredCategoryData}
              selectedCategory={selectedCategory}
              setSelectedCategory={handleCategoryChange}
              handleAddInvestment={handleAddInvestment}
              handleViewTransactionHistory={handleViewTransactionHistoryForTab}
              formatCompactCurrency={formatCompactCurrency}
              formatCurrency={formatCurrency}
              formatPercentage={formatPercentage}
              searchQuery={searchQuery}
              hasSearchResults={hasSearchResults}
              showValues={showValues}
              // Transaction history props
              transactionHistory={transactionHistory}
              showTransactionHistory={showTransactionHistory}
              selectedInvestment={selectedInvestment}
              handleAddTransaction={handleAddTransaction}
              handleEditTransaction={handleEditTransaction}
              handleDeleteTransaction={handleDeleteTransaction}
              handleEditInvestment={handleEditInvestment}
              handleDeleteInvestment={handleDeleteInvestment}
              handleCloseTransactionHistory={handleCloseTransactionHistory}
              transactionMenuAnchor={transactionMenuAnchor}
              transactionMenuId={transactionMenuId}
              handleTransactionMenuOpen={handleTransactionMenuOpen}
              handleTransactionMenuClose={handleTransactionMenuClose}
            />

            {/* Desktop Transaction History */}
            {showTransactionHistory && isMobile && (
              <Box
                sx={{
                  width: 400,
                  flexShrink: 0,
                  flexGrow: 0,
                }}
              >
                <TransactionHistory
                  selectedInvestment={selectedInvestment}
                  transactionHistory={transactionHistory}
                  handleAddTransaction={handleAddTransaction}
                  handleTransactionMenuOpen={handleTransactionMenuOpen}
                  formatCurrency={formatTransactionCurrency}
                  formatDate={formatDate}
                  getTransactionIcon={getTransactionIcon}
                  getTransactionColor={getTransactionColor}
                  onClose={handleCloseTransactionHistory}
                />
              </Box>
            )}
          </Box>
        )}

        {/* Add Investment Dialog */}
        {addDialogOpen &&
          selectedCategory &&
          categoryAmendConfigs[selectedCategory] && (
            <InvestmentAmendDialogSkeleton
              dialogOpen={addDialogOpen}
              dialogType="add"
              formData={formData}
              setFormData={setFormData}
              handleSave={handleSaveInvestment}
              handleClose={() => setAddDialogOpen(false)}
              selectedCategory={selectedCategory}
              selectedCategoryInfo={selectedCategoryInfo}
              amendConfig={categoryAmendConfigs[selectedCategory]}
              categoryKey={selectedCategory}
            />
          )}
        {/* Edit Investment Dialog */}
        {editDialogOpen &&
          selectedCategory &&
          categoryAmendConfigs[selectedCategory] && (
            <InvestmentAmendDialogSkeleton
              dialogOpen={editDialogOpen}
              dialogType="edit"
              formData={formData}
              setFormData={setFormData}
              handleSave={handleSaveEdit}
              handleClose={() => setEditDialogOpen(false)}
              selectedCategory={selectedCategory}
              selectedCategoryInfo={selectedCategoryInfo}
              amendConfig={categoryAmendConfigs[selectedCategory]}
              categoryKey={selectedCategory}
            />
          )}

        {/* Delete Confirmation Dialog */}
        <DeleteConfirmationDialog
          deleteDialogOpen={deleteDialogOpen || deleteTransactionDialogOpen}
          selectedInvestment={
            deleteDialogOpen ? selectedInvestment : selectedTransaction
          }
          handleConfirmDelete={
            deleteDialogOpen
              ? handleConfirmDelete
              : handleConfirmDeleteTransaction
          }
          handleClose={() => {
            if (deleteDialogOpen) setDeleteDialogOpen(false);
            if (deleteTransactionDialogOpen)
              setDeleteTransactionDialogOpen(false);
          }}
        />

        {/* Transaction History Dialog */}
        <TransactionAmendDialogSkeleton
          open={transactionDialogOpen}
          onClose={() => setTransactionDialogOpen(false)}
          investment={selectedInvestment}
          onSave={handleSaveTransaction}
          editingTransaction={
            transactionDialogType === "edit" ? selectedTransaction : null
          }
        />
      </Container>
    </Box>
  );
};

export default DashboardPage;
