import React, { useState, useRef, useEffect } from "react";
import {
  Box,
  Typography,
  Avatar,
  Chip,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  IconButton,
  Tabs,
  Tab,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Button,
} from "@mui/material";
import { ExpandMore, Edit, Delete } from "@mui/icons-material";
import { DASHBOARD } from "../../constants/dashboard";
import {
  formatTransactionCurrency,
  formatTransactionDate,
} from "../../utils/categoryViewUtils";
import {
  getTransactionIcon,
  getTransactionColor,
} from "../../utils/transactionUtils";
import InvestmentMobileSkeleton from "./InvestmentMobileSkeleton";
import {
  FIXED_DEPOSIT_MOBILE_VIEW_DATA,
  MUTUAL_FUNDS_MOBILE_VIEW_DATA,
  STOCKS_MOBILE_VIEW_DATA,
  PPF_MOBILE_VIEW_DATA,
  EPF_MOBILE_VIEW_DATA,
  NPS_MOBILE_VIEW_DATA,
  BONDS_MOBILE_VIEW_DATA,
  GOLD_MOBILE_VIEW_DATA,
  REAL_ESTATE_MOBILE_VIEW_DATA,
  CRYPTO_MOBILE_VIEW_DATA,
  OTHERS_MOBILE_VIEW_DATA,
} from "../../constants/dashboard/common/InvestmentMobileSkeletonConstants";
import DeleteConfirmationDialog from "./common/DeleteConfirmationDialog";

const CategoriesMobileDropdown = ({
  filteredCategoryData,
  expandedCategory,
  handleAddInvestment,
  handleInvestmentMenuOpen,
  handleViewTransactionHistory,
  formatCompactCurrency,
  formatPercentage,
  searchQuery,
  hasSearchResults,
  showValues,
  // Transaction history props
  transactionHistory,
  handleEditTransaction,
  handleDeleteTransaction,
  // Investment deletion prop
  handleDeleteInvestment,
  // Investment edit prop
  handleEditInvestment,
  // Selected investment prop
  selectedInvestment: selectedInvestmentProp,
  handleAddTransaction,
}) => {
  const [activeTab, setActiveTab] = useState(0); // 0: Assets, 1: History
  const [isHistoryEnabled, setIsHistoryEnabled] = useState(false);
  const [selectedCategory, setSelectedCategory] = useState(null);
  const [isDropdownExpanded, setIsDropdownExpanded] = useState(false);
  const swipeableRef = useRef(null);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [investmentToDelete, setInvestmentToDelete] = useState(null);
  const [transactionDeleteDialogOpen, setTransactionDeleteDialogOpen] =
    useState(false);
  const [selectedTransaction, setSelectedTransaction] = useState(null);

  // Set default selected category on mount and handle search changes
  useEffect(() => {
    if (Object.keys(filteredCategoryData).length > 0) {
      // If no category is selected or current category is not in filtered data
      if (!selectedCategory || !filteredCategoryData[selectedCategory]) {
        const firstCategory = Object.keys(filteredCategoryData)[0];
        setSelectedCategory(firstCategory);
        setIsHistoryEnabled(false);
        setActiveTab(0);
      }
    }
  }, [filteredCategoryData, selectedCategory]); // eslint-disable-line react-hooks/exhaustive-deps

  // Handle search functionality - update selectedCategory when expandedCategory changes
  useEffect(() => {
    if (expandedCategory && expandedCategory !== selectedCategory) {
      setSelectedCategory(expandedCategory);
      setIsHistoryEnabled(false);
      setActiveTab(0);
    }
  }, [expandedCategory]); // eslint-disable-line react-hooks/exhaustive-deps

  const handleTabChange = (event, newValue) => {
    setActiveTab(newValue);
  };

  const handleHistoryButtonClick = (item) => {
    setIsHistoryEnabled(true);
    setActiveTab(1);
    handleViewTransactionHistory(item);
  };

  const handleSwipeLeft = () => {
    if (activeTab === 1) {
      setActiveTab(0);
    }
  };

  const handleTouchStart = (e) => {
    const touch = e.touches[0];
    swipeableRef.current.touchStartX = touch.clientX;
  };

  const handleTouchEnd = (e) => {
    const touch = e.changedTouches[0];
    const diff = swipeableRef.current.touchStartX - touch.clientX;
    const threshold = 90;

    if (Math.abs(diff) > threshold) {
      if (diff > 0) {
        // Swipe right - currently blocked because on swipe left
        // history, edit, delete menu will appear.
        // handleSwipeRight();
      } else {
        // Swipe left
        handleSwipeLeft();
      }
    }
  };

  const handleCategorySelect = (categoryKey) => {
    setSelectedCategory(categoryKey);
    setIsHistoryEnabled(false);
    setActiveTab(0);
    setIsDropdownExpanded(false);
  };

  // Handler to open delete dialog
  const handleOpenDeleteDialog = (investment) => {
    setInvestmentToDelete(investment);
    setDeleteDialogOpen(true);
  };
  // Handler to confirm delete investment
  const handleConfirmDelete = () => {
    if (handleDeleteInvestment && investmentToDelete) {
      handleDeleteInvestment(investmentToDelete);
    }
    setDeleteDialogOpen(false);
    setInvestmentToDelete(null);
  };

  // Handler to confirm delete transaction
  const handleConfirmDeleteTransaction = () => {
    if (handleDeleteTransaction && selectedTransaction) {
      handleDeleteTransaction(selectedTransaction);
    }
    setTransactionDeleteDialogOpen(false);
    setSelectedTransaction(null);
  };

  // Handler to close transaction delete dialog
  const handleCloseTransactionDeleteDialog = () => {
    setTransactionDeleteDialogOpen(false);
    setSelectedTransaction(null);
  };

  // Handler to open transaction delete dialog
  const handleDeleteTransactionClick = (transaction) => {
    setSelectedTransaction(transaction);
    setTransactionDeleteDialogOpen(true);
  };

  const categoryConfigs = {
    FIXED_DEPOSITS: FIXED_DEPOSIT_MOBILE_VIEW_DATA,
    MUTUAL_FUNDS: MUTUAL_FUNDS_MOBILE_VIEW_DATA,
    STOCKS: STOCKS_MOBILE_VIEW_DATA,
    PPF: PPF_MOBILE_VIEW_DATA,
    EPF: EPF_MOBILE_VIEW_DATA,
    NPS: NPS_MOBILE_VIEW_DATA,
    BONDS: BONDS_MOBILE_VIEW_DATA,
    GOLD: GOLD_MOBILE_VIEW_DATA,
    REAL_ESTATE: REAL_ESTATE_MOBILE_VIEW_DATA,
    CRYPTO: CRYPTO_MOBILE_VIEW_DATA,
    OTHERS: OTHERS_MOBILE_VIEW_DATA,
  };

  // Tabbed design for all categories
  const renderTabbedCategoryView = () => {
    if (!selectedCategory) return null;
    const categoryInfo = DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory];
    const selectedCategoryInfo =
      DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory];
    const categoryData = filteredCategoryData[selectedCategory] || {
      totalValue: 0,
      totalInvested: 0,
      returns: 0,
      returnPercentage: 0,
      items: [],
    };
    return (
      <Box
        ref={swipeableRef}
        onTouchStart={handleTouchStart}
        onTouchEnd={handleTouchEnd}
        sx={{
          borderRadius: 2,
          boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
          bgcolor: "#1a1a1a",
          border: "1px solid #333",
          overflow: "hidden",
          minHeight: "50vh",
          display: "flex",
          flexDirection: "column",
        }}
      >
        {/* Tabs Header */}
        <Box sx={{ bgcolor: "#2d2d2d", borderBottom: "1px solid #444" }}>
          <Tabs
            value={activeTab}
            onChange={handleTabChange}
            sx={{
              "& .MuiTab-root": {
                color: "#aaa",
                fontWeight: 600,
                "&.Mui-selected": {
                  color: "white",
                },
              },
              "& .MuiTabs-indicator": {
                bgcolor: categoryInfo.color,
              },
            }}
          >
            <Tab label="Assets" disabled={false} />
            <Tab
              label="History"
              disabled={!isHistoryEnabled}
              sx={{
                "&.Mui-disabled": {
                  color: "#555",
                },
              }}
            />
          </Tabs>
        </Box>
        {/* Tab Content */}
        <Box sx={{ flex: 1, overflow: "hidden" }}>
          {activeTab === 0 && (
            <InvestmentMobileSkeleton
              viewData={categoryConfigs[selectedCategory]}
              categoryData={categoryData}
              onAdd={() => handleAddInvestment(selectedCategory)}
              onAction={(actionKey, item, event) => {
                if (actionKey === "history") handleHistoryButtonClick(item);
                if (actionKey === "menu")
                  handleInvestmentMenuOpen(event, item.id);
                if (actionKey === "edit") handleEditInvestment(item);
                if (actionKey === "delete") handleOpenDeleteDialog(item);
              }}
              selectedCategoryInfo={selectedCategoryInfo}
              showValues={showValues}
            />
          )}
          {activeTab === 1 && renderHistoryTab()}
        </Box>
      </Box>
    );
  };

  const renderHistoryTab = () => {
    if (!selectedInvestmentProp || !transactionHistory) {
      return (
        <Box sx={{ p: 3, textAlign: "center" }}>
          <Typography variant="body2" sx={{ color: "#aaa" }}>
            No transaction history available
          </Typography>
        </Box>
      );
    }

    return (
      <Box sx={{ height: "100%", overflow: "auto" }}>
        <Box
          sx={{
            p: 3,
            bgcolor: "#2d2d2d",
            display: "flex",
            alignItems: "center",
            gap: 2,
          }}
        >
          <Typography variant="h6" sx={{ fontWeight: 600, color: "white" }}>
            Transaction History
          </Typography>
          <Typography variant="body2" sx={{ color: "#aaa", ml: "auto" }}>
            {selectedInvestmentProp.name}
          </Typography>
        </Box>

        {/* Add Transaction Button */}
        <Box
          sx={{
            px: 3,
            pb: 3,
            bgcolor: "#2d2d2d",
          }}
        >
          <Button
            onClick={() =>
              handleAddTransaction &&
              handleAddTransaction(selectedInvestmentProp)
            }
            variant="contained"
            fullWidth
            sx={{
              bgcolor: "#4caf50",
              color: "white",
              py: 0.5,
              fontWeight: 600,
              textTransform: "none",
              fontSize: "1.1rem",
              "&:hover": {
                bgcolor: "#45a049",
              },
            }}
          >
            + Add Transaction
          </Button>
        </Box>

        <Box sx={{ p: 0 }}>
          {transactionHistory.length > 0 ? (
            <List sx={{ p: 0 }}>
              {transactionHistory.map((transaction, index) => (
                <React.Fragment key={transaction.id}>
                  <ListItem
                    sx={{
                      px: 3,
                      py: 2,
                      flexDirection: "column",
                      alignItems: "flex-start",
                    }}
                  >
                    <Box
                      sx={{
                        display: "flex",
                        alignItems: "center",
                        width: "100%",
                        mb: 1,
                      }}
                    >
                      <ListItemAvatar>
                        <Avatar
                          sx={{
                            bgcolor: getTransactionColor(
                              transaction.transactionType
                            ),
                            width: 40,
                            height: 40,
                            fontSize: "1rem",
                          }}
                        >
                          {getTransactionIcon(transaction.transactionType)}
                        </Avatar>
                      </ListItemAvatar>
                      <ListItemText
                        primary={
                          <Box
                            sx={{
                              display: "flex",
                              alignItems: "center",
                              gap: 1,
                            }}
                          >
                            <Typography
                              variant="subtitle1"
                              sx={{ fontWeight: 600, color: "white" }}
                            >
                              {transaction.transactionType}
                            </Typography>
                          </Box>
                        }
                        secondary={
                          <Typography variant="body2" sx={{ color: "#aaa" }}>
                            {formatTransactionDate(
                              transaction.transactionDate,
                              showValues
                            )}
                          </Typography>
                        }
                      />
                      <Box
                        sx={{ display: "flex", alignItems: "center", gap: 1 }}
                      >
                        <Typography
                          variant="h6"
                          sx={{
                            fontWeight: 600,
                            color: "white",
                            mr: 1,
                          }}
                        >
                          {formatTransactionCurrency(
                            transaction.amount,
                            showValues
                          )}
                        </Typography>
                        <IconButton
                          onClick={() =>
                            handleEditTransaction &&
                            handleEditTransaction(transaction)
                          }
                          sx={{ color: "#f0a500" }}
                          size="small"
                        >
                          <Edit />
                        </IconButton>
                        <IconButton
                          onClick={() =>
                            handleDeleteTransactionClick(transaction)
                          }
                          sx={{ color: "#f44336" }}
                          size="small"
                        >
                          <Delete />
                        </IconButton>
                      </Box>
                    </Box>

                    <Box sx={{ width: "100%", textAlign: "left" }}>
                      {transaction.description && (
                        <Typography
                          variant="body2"
                          sx={{
                            color: "#aaa",
                            mb: 0.5,
                          }}
                        >
                          {transaction.description}
                        </Typography>
                      )}
                      {transaction.quantity && (
                        <Typography variant="body2" sx={{ color: "#aaa" }}>
                          Quantity: {transaction.quantity}
                        </Typography>
                      )}
                    </Box>
                  </ListItem>
                  {index < transactionHistory.length - 1 && (
                    <Box sx={{ height: 1, bgcolor: "#444", mx: 3 }} />
                  )}
                </React.Fragment>
              ))}
            </List>
          ) : (
            <Box sx={{ textAlign: "center", py: 4 }}>
              <Typography variant="body2" sx={{ color: "#aaa" }}>
                No transactions found for this investment
              </Typography>
            </Box>
          )}
        </Box>
      </Box>
    );
  };

  const renderCategoryDropdown = () => (
    <Box sx={{ mb: 3 }}>
      <Accordion
        expanded={isDropdownExpanded}
        onChange={() => setIsDropdownExpanded(!isDropdownExpanded)}
        sx={{
          bgcolor: "#2d2d2d",
          color: "white",
          "&:before": { display: "none" },
          "& .MuiAccordionSummary-root": {
            bgcolor: "#2d2d2d",
            color: "white",
            "&:hover": { bgcolor: "#3d3d3d" },
          },
          "& .MuiAccordionDetails-root": {
            bgcolor: "#2d2d2d",
            p: 0,
          },
        }}
      >
        <AccordionSummary
          expandIcon={<ExpandMore sx={{ color: "white" }} />}
          sx={{
            "& .MuiAccordionSummary-content": {
              alignItems: "center",
              gap: 2,
            },
          }}
        >
          <Avatar
            sx={{
              bgcolor: selectedCategory
                ? DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory]?.color
                : "#666",
              width: 40,
              height: 40,
              fontSize: "1.2rem",
            }}
          >
            {selectedCategory
              ? DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory]?.icon
              : "?"}
          </Avatar>
          <Typography variant="h6" sx={{ fontWeight: 600, color: "white" }}>
            {selectedCategory
              ? DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory]?.name
              : "Select Category"}
          </Typography>
        </AccordionSummary>
        <AccordionDetails>
          {Object.entries(DASHBOARD.INVESTMENT_CATEGORIES).map(
            ([categoryKey, category]) => {
              const data = filteredCategoryData[categoryKey] || {
                totalValue: 0,
                totalInvested: 0,
                returns: 0,
                returnPercentage: 0,
                items: [],
              };
              const hasInvestments = data.totalValue > 0;
              const isSelected = selectedCategory === categoryKey;

              // Skip categories that don't exist in filtered data when searching
              if (searchQuery.trim() && !filteredCategoryData[categoryKey]) {
                return null;
              }

              return (
                <Box
                  key={categoryKey}
                  onClick={(e) => {
                    e.stopPropagation(); // Prevent accordion toggle
                    handleCategorySelect(categoryKey);
                  }}
                  sx={{
                    p: 3,
                    borderBottom: "1px solid #444",
                    cursor: "pointer",
                    bgcolor: isSelected ? "#3d3d3d" : "#2d2d2d",
                    "&:hover": {
                      bgcolor: isSelected ? "#3d3d3d" : "#3a3a3a",
                    },
                    "&:last-child": {
                      borderBottom: "none",
                    },
                  }}
                >
                  <Box
                    sx={{
                      display: "flex",
                      alignItems: "center",
                      gap: 2,
                      width: "100%",
                    }}
                  >
                    <Avatar
                      sx={{
                        bgcolor: category.color,
                        width: 40,
                        height: 40,
                        fontSize: "1.2rem",
                      }}
                    >
                      {category.icon}
                    </Avatar>
                    <Box sx={{ flex: 1 }}>
                      <Typography
                        variant="h6"
                        sx={{ fontWeight: 600, color: "white" }}
                      >
                        {category.name}
                      </Typography>
                      <Typography variant="body2" sx={{ color: "#aaa" }}>
                        {hasInvestments
                          ? formatCompactCurrency(data.totalValue)
                          : "No investments"}
                      </Typography>
                    </Box>
                    {hasInvestments && (
                      <Chip
                        label={formatPercentage(data.returnPercentage)}
                        size="small"
                        sx={{
                          bgcolor:
                            data.returnPercentage >= 0 ? "#e8f5e8" : "#ffebee",
                          color:
                            data.returnPercentage >= 0 ? "#2e7d32" : "#c62828",
                          fontWeight: 600,
                          fontSize: "0.75rem",
                        }}
                      />
                    )}
                  </Box>
                </Box>
              );
            }
          )}
        </AccordionDetails>
      </Accordion>
    </Box>
  );

  return (
    <Box sx={{ mt: 3 }}>
      {searchQuery.trim() && !hasSearchResults ? (
        <Box sx={{ p: 3, textAlign: "center" }}>
          <Typography variant="body2" sx={{ color: "#aaa" }}>
            No results found
          </Typography>
        </Box>
      ) : (
        <>
          {renderCategoryDropdown()}
          <Box>{renderTabbedCategoryView()}</Box>
          <DeleteConfirmationDialog
            deleteDialogOpen={deleteDialogOpen}
            selectedInvestment={investmentToDelete}
            handleConfirmDelete={handleConfirmDelete}
            handleClose={() => setDeleteDialogOpen(false)}
          />
          <DeleteConfirmationDialog
            deleteDialogOpen={transactionDeleteDialogOpen}
            selectedInvestment={selectedTransaction}
            handleConfirmDelete={handleConfirmDeleteTransaction}
            handleClose={handleCloseTransactionDeleteDialog}
          />
        </>
      )}
    </Box>
  );
};

export default CategoriesMobileDropdown;
