import {
  TrendingUp,
  TrendingDown,
  AccountBalance,
  Payment,
  AttachMoney,
  RemoveCircle,
} from '@mui/icons-material';

// Transaction type colors
export const getTransactionColor = (transactionType) => {
  switch (transactionType) {
    case 'BUY':
    case 'DEPOSIT':
      return '#f44336'; // Red for debit
    case 'SELL':
    case 'WITHDRAWAL':
      return '#4caf50'; // Green for credit
    case 'DIVIDEND':
    case 'INTEREST':
      return '#2196f3'; // Blue for income
    default:
      return '#9e9e9e'; // Gray for others
  }
};

// Transaction type icons
export const getTransactionIcon = (transactionType) => {
  switch (transactionType) {
    case 'BUY':
      return <TrendingUp />;
    case 'SELL':
      return <TrendingDown />;
    case 'DIVIDEND':
    case 'INTEREST':
      return <Payment />;
    case 'DEPOSIT':
      return <AccountBalance />;
    case 'WITHDRAWAL':
      return <RemoveCircle />;
    default:
      return <AttachMoney />;
  }
};

// Format date for display
export const formatDate = (date) => {
  if (!date) return '';
  
  const dateObj = new Date(date);
  const now = new Date();
  const diffTime = Math.abs(now - dateObj);
  const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24));
  
  if (diffDays === 1) {
    return 'Today';
  } else if (diffDays === 2) {
    return 'Yesterday';
  } else if (diffDays <= 7) {
    return `${diffDays - 1} days ago`;
  } else {
    return dateObj.toLocaleDateString('en-IN', {
      year: 'numeric',
      month: 'short',
      day: 'numeric'
    });
  }
};

// Format currency for display
export const formatCurrency = (amount) => {
  if (!amount) return '0';
  
  return new Intl.NumberFormat('en-IN', {
    style: 'currency',
    currency: 'INR',
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount);
};

