// Calculate transaction summary
export const calculateTransactionSummary = (transactions) => {
  if (!transactions || transactions.length === 0) {
    return {
      totalBuy: 0,
      totalSell: 0,
      totalDividend: 0,
      totalInterest: 0,
      totalDeposit: 0,
      totalWithdrawal: 0,
      netAmount: 0,
    };
  }

  const summary = transactions.reduce((acc, transaction) => {
    const amount = transaction.amount || 0;
    
    switch (transaction.transactionType) {
      case 'BUY':
        acc.totalBuy += amount;
        break;
      case 'SELL':
        acc.totalSell += amount;
        break;
      case 'DIVIDEND':
        acc.totalDividend += amount;
        break;
      case 'INTEREST':
        acc.totalInterest += amount;
        break;
      case 'DEPOSIT':
        acc.totalDeposit += amount;
        break;
      case 'WITHDRAWAL':
        acc.totalWithdrawal += amount;
        break;
      default:
        // Handle any unknown transaction types
        break;
    }
    
    return acc;
  }, {
    totalBuy: 0,
    totalSell: 0,
    totalDividend: 0,
    totalInterest: 0,
    totalDeposit: 0,
    totalWithdrawal: 0,
  });

  summary.netAmount = (summary.totalSell + summary.totalDividend + summary.totalInterest + summary.totalWithdrawal) - 
                      (summary.totalBuy + summary.totalDeposit);

  return summary;
}; 