// Common utilities for category view skeletons

// Base formatting functions (without showValues logic)
export const formatPercentageBase = (percentage) => {
  if (percentage === undefined || percentage === null) return "0.00%";
  return `${percentage.toFixed(2)}%`;
};

export const formatCurrencyBase = (amount) => {
  if (amount === undefined || amount === null) return "\u20b90";
  return new Intl.NumberFormat("en-IN", {
    style: "currency",
    currency: "INR",
    minimumFractionDigits: 0,
    maximumFractionDigits: 0,
  }).format(amount);
};

export const formatCompactCurrencyBase = (amount) => {
  if (amount === undefined || amount === null) return "\u20b90";
  if (amount >= 10000000) {
    return `\u20b9${(amount / 10000000).toFixed(1)}Cr`;
  } else if (amount >= 100000) {
    return `\u20b9${(amount / 100000).toFixed(1)}L`;
  } else if (amount >= 1000) {
    return `\u20b9${(amount / 1000).toFixed(0)}K`;
  }
  return formatCurrencyBase(amount);
};

export const formatCurrencyHighlightBase = (amount) => {
  return formatCurrencyBase(amount);
};

export const formatDateBase = (dateValue) => {
  if (!dateValue) return "";
  const date = new Date(dateValue);
  return date.toLocaleDateString("en-IN", {
    year: "numeric",
    month: "short",
    day: "numeric",
  });
};

export const formatDatePartsBase = (dateValue) => {
  if (!dateValue) return { dayMonth: "", year: "" };
  const date = new Date(dateValue);
  const dayMonth = date.toLocaleDateString("en-IN", {
    day: "2-digit",
    month: "short",
  });
  const year = date.getFullYear();
  return { dayMonth, year };
};

// Wrapper functions that respect showValues state
export const formatPercentage = (percentage, showValues = true) => {
  if (!showValues) return "";
  return formatPercentageBase(percentage);
};

export const formatCurrency = (amount, showValues = true) => {
  if (!showValues) return "";
  return formatCurrencyBase(amount);
};

export const formatCompactCurrency = (amount, showValues = true) => {
  if (!showValues) return "";
  return formatCompactCurrencyBase(amount);
};

export const formatCurrencyHighlight = (amount, showValues = true) => {
  if (!showValues) return "";
  return formatCurrencyHighlightBase(amount);
};

export const formatDate = (dateValue, showValues = true) => {
  // Date formatting doesn't need to be masked as it's not sensitive financial data
  return formatDateBase(dateValue);
};

export const formatDateParts = (dateValue, showValues = true) => {
  // Date formatting doesn't need to be masked as it's not sensitive financial data
  return formatDatePartsBase(dateValue);
};

export const renderAvatarContent = (selectedName, selectedAvatarIcon) => {
  return selectedAvatarIcon || `${selectedName?.split(" ")[0][0]}`;
};

export const formatFieldData = (field, fieldData, showValues = true) => {
  const { type, valueKey } = field;
  let formattedValue;
  let formattedColor = "white";

  if (type === "currencyCompact") {
    formattedValue = formatCompactCurrency(fieldData[valueKey], showValues);
  } else if (type === "currency") {
    formattedValue = formatCurrency(fieldData[valueKey], showValues);
  } else if (type === "currencyHighlight") {
    formattedValue = formatCurrencyHighlight(fieldData[valueKey], showValues);
  } else if (type === "percentage") {
    formattedValue = formatPercentage(fieldData[valueKey], showValues);
    formattedColor =
      fieldData[valueKey] > 0
        ? "#4caf50"
        : fieldData[valueKey] < 0
        ? "#f44336"
        : "white";
  } else if (type === "date") {
    formattedValue = formatDate(fieldData[valueKey], showValues);
  } else if (valueKey === "itemsCount") {
    formattedValue = fieldData.items?.length || 0;
  } else {
    formattedValue = fieldData[valueKey];
  }
  return { formattedValue, formattedColor };
};

// Transaction-specific formatting functions
export const formatTransactionCurrency = (amount, showValues = true) => {
  if (!showValues) return "";
  return formatCurrencyBase(amount);
};

export const formatTransactionDate = (dateValue, showValues = true) => {
  // Date formatting doesn't need to be masked as it's not sensitive financial data
  return formatDateBase(dateValue);
};
