import React from "react";
import {
  Box,
  Paper,
  Typography,
  IconButton,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  Chip,
  Button,
  List as InvestmentList,
  ListItem as InvestmentListItem,
  ListItemText as InvestmentListItemText,
  ListItemAvatar as InvestmentListItemAvatar,
  IconButton as InvestmentIconButton,
} from "@mui/material";
import { GridView, Add, MoreVert, History } from "@mui/icons-material";
import { DASHBOARD } from "../../constants/dashboard";
import InvestmentWebSkeleton from "./InvestmentWebSkeleton";
import {
  FIXED_DEPOSIT_WEB_VIEW_DATA,
  MUTUAL_FUNDS_WEB_VIEW_DATA,
  STOCKS_WEB_VIEW_DATA,
  PPF_WEB_VIEW_DATA,
  EPF_WEB_VIEW_DATA,
  NPS_WEB_VIEW_DATA,
  BONDS_WEB_VIEW_DATA,
  GOLD_WEB_VIEW_DATA,
  REAL_ESTATE_WEB_VIEW_DATA,
  CRYPTO_WEB_VIEW_DATA,
  LIC_WEB_VIEW_DATA,
  OTHERS_WEB_VIEW_DATA,
} from "../../constants/dashboard/common/InvestmentWebSkeletonConstants";

const CategoriesWebSidebar = ({
  filteredCategoryData,
  selectedCategory,
  setSelectedCategory,
  handleAddInvestment,
  handleInvestmentMenuOpen,
  handleViewTransactionHistory,
  formatCompactCurrency,
  formatCurrency,
  formatPercentage,
  searchQuery,
  hasSearchResults,
  showValues,
  // Transaction history props
  transactionHistory,
  showTransactionHistory,
  selectedInvestment,
  handleAddTransaction,
  handleEditTransaction,
  handleDeleteTransaction,
  handleEditInvestment,
  handleDeleteInvestment,
  handleCloseTransactionHistory,
  transactionMenuAnchor,
  transactionMenuId,
  handleTransactionMenuOpen,
  handleTransactionMenuClose,
}) => {
  const selectedCategoryData = filteredCategoryData[selectedCategory] || {
    totalValue: 0,
    totalInvested: 0,
    returns: 0,
    returnPercentage: 0,
    items: [],
  };
  const selectedCategoryInfo =
    DASHBOARD.INVESTMENT_CATEGORIES[selectedCategory];

  // Component mapping for different categories
  const categoryConfigs = {
    FIXED_DEPOSITS: FIXED_DEPOSIT_WEB_VIEW_DATA,
    MUTUAL_FUNDS: MUTUAL_FUNDS_WEB_VIEW_DATA,
    STOCKS: STOCKS_WEB_VIEW_DATA,
    PPF: PPF_WEB_VIEW_DATA,
    EPF: EPF_WEB_VIEW_DATA,
    NPS: NPS_WEB_VIEW_DATA,
    BONDS: BONDS_WEB_VIEW_DATA,
    GOLD: GOLD_WEB_VIEW_DATA,
    REAL_ESTATE: REAL_ESTATE_WEB_VIEW_DATA,
    CRYPTO: CRYPTO_WEB_VIEW_DATA,
    LIC: LIC_WEB_VIEW_DATA,
    OTHERS: OTHERS_WEB_VIEW_DATA,
  };

  return (
    <Box
      sx={{
        display: "flex",
        gap: 3,
        flexDirection: "row",
        width: "100%",
      }}
    >
      {/* Left Sidebar - Investment Categories */}
      <Box
        sx={{
          width: 300,
          flexShrink: 0,
        }}
      >
        <Paper
          sx={{
            borderRadius: 2,
            overflow: "hidden",
            boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
            bgcolor: "#1a1a1a",
            border: "1px solid #333",
          }}
        >
          <Box
            sx={{
              p: 2,
              bgcolor: "#2d2d2d",
              color: "white",
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              borderBottom: "1px solid #444",
            }}
          >
            <Typography variant="h6" sx={{ fontWeight: 600 }}>
              Assets
            </Typography>
            <IconButton size="small" sx={{ color: "white" }}>
              <GridView />
            </IconButton>
          </Box>

          <List sx={{ p: 0 }}>
            {searchQuery.trim() && !hasSearchResults ? (
              <Box sx={{ p: 3, textAlign: "center" }}>
                <Typography variant="body2" sx={{ color: "#aaa" }}>
                  No results found
                </Typography>
              </Box>
            ) : (
              Object.entries(DASHBOARD.INVESTMENT_CATEGORIES).map(
                ([categoryKey, category]) => {
                  const data = filteredCategoryData[categoryKey] || {
                    totalValue: 0,
                    totalInvested: 0,
                    returns: 0,
                    returnPercentage: 0,
                    items: [],
                  };
                  const hasInvestments = data.totalValue > 0;
                  const isSelected = selectedCategory === categoryKey;

                  // Skip categories that don't exist in filtered data when searching
                  if (
                    searchQuery.trim() &&
                    !filteredCategoryData[categoryKey]
                  ) {
                    return null;
                  }

                  return (
                    <ListItem
                      key={categoryKey}
                      selected={isSelected}
                      onClick={() => setSelectedCategory(categoryKey)}
                      sx={{
                        borderBottom: "1px solid #333",
                        bgcolor: isSelected ? "#2d2d2d" : "#1a1a1a",
                        py: 1.5,
                        color: "white",
                        "&:hover": {
                          bgcolor: isSelected ? "#2d2d2d" : "#2a2a2a",
                        },
                        "&.Mui-selected": {
                          bgcolor: "#2d2d2d",
                          "&:hover": {
                            bgcolor: "#2d2d2d",
                          },
                        },
                      }}
                    >
                      <ListItemAvatar>
                        <Avatar
                          sx={{
                            bgcolor: category.color,
                            width: 40,
                            height: 40,
                            fontSize: "1.2rem",
                          }}
                        >
                          {category.icon}
                        </Avatar>
                      </ListItemAvatar>
                      <ListItemText
                        primary={
                          <Typography
                            variant="subtitle1"
                            sx={{ fontWeight: 600, color: "white" }}
                          >
                            {category.name}
                          </Typography>
                        }
                        secondary={
                          <Typography variant="body2" sx={{ color: "#aaa" }}>
                            {hasInvestments
                              ? formatCompactCurrency(data.totalValue)
                              : "No investments"}
                          </Typography>
                        }
                      />
                      {hasInvestments && (
                        <Chip
                          label={formatPercentage(data.returnPercentage)}
                          size="small"
                          sx={{
                            bgcolor:
                              data.returnPercentage >= 0
                                ? "#e8f5e8"
                                : "#ffebee",
                            color:
                              data.returnPercentage >= 0
                                ? "#2e7d32"
                                : "#c62828",
                            fontWeight: 600,
                            fontSize: "0.75rem",
                          }}
                        />
                      )}
                    </ListItem>
                  );
                }
              )
            )}
          </List>
        </Paper>
      </Box>

      {/* Right Side - Category Details */}
      {selectedCategory && categoryConfigs[selectedCategory] ? (
        <InvestmentWebSkeleton
          viewData={categoryConfigs[selectedCategory]}
          categoryData={selectedCategoryData}
          onAdd={() => handleAddInvestment(selectedCategory)}
          onAction={(actionKey, item, event) => {
            if (actionKey === "menu") handleInvestmentMenuOpen(event, item.id);
            else if (actionKey === "edit") handleEditInvestment(item);
            else if (actionKey === "delete") handleDeleteInvestment(item);
            // Removed handleViewTransactionHistory call - history button now switches to tab
          }}
          selectedCategoryInfo={selectedCategoryInfo}
          showValues={showValues}
          // Transaction history props
          transactionHistory={transactionHistory}
          showTransactionHistory={showTransactionHistory}
          selectedInvestment={selectedInvestment}
          handleAddTransaction={handleAddTransaction}
          handleEditTransaction={handleEditTransaction}
          handleDeleteTransaction={handleDeleteTransaction}
          handleCloseTransactionHistory={handleCloseTransactionHistory}
          transactionMenuAnchor={transactionMenuAnchor}
          transactionMenuId={transactionMenuId}
          handleTransactionMenuOpen={handleTransactionMenuOpen}
          handleTransactionMenuClose={handleTransactionMenuClose}
          handleViewTransactionHistory={handleViewTransactionHistory}
        />
      ) : (
        <Box
          sx={{
            flexGrow: 1,
            minWidth: 0, // Allow flex item to shrink below content size
            transition: "width 0.3s ease-in-out",
            display: "flex",
            flexDirection: "column",
          }}
        >
          <Paper
            sx={{
              borderRadius: 2,
              overflow: "hidden",
              boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
              bgcolor: "#1a1a1a",
              border: "1px solid #333",
              flex: 1,
              display: "flex",
              flexDirection: "column",
            }}
          >
            <Box
              sx={{
                p: 3,
                bgcolor: "#2d2d2d",
                borderBottom: "1px solid #444",
              }}
            >
              <Box
                sx={{
                  display: "flex",
                  justifyContent: "space-between",
                  alignItems: "flex-start",
                  mb: 3,
                  gap: 2,
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    gap: 2,
                    flex: 1,
                  }}
                >
                  <Avatar
                    sx={{
                      bgcolor: selectedCategoryInfo?.color,
                      width: 48,
                      height: 48,
                      fontSize: "1.5rem",
                    }}
                  >
                    {selectedCategoryInfo?.icon}
                  </Avatar>
                  <Box sx={{ flex: 1 }}>
                    <Typography
                      variant="h5"
                      sx={{ fontWeight: 600, color: "white", mb: 0.5 }}
                    >
                      {selectedCategoryInfo?.name}
                    </Typography>
                    <Typography variant="body2" sx={{ color: "#aaa" }}>
                      {selectedCategoryInfo?.description}
                    </Typography>
                  </Box>
                </Box>
                <Button
                  variant="contained"
                  startIcon={<Add />}
                  onClick={() => handleAddInvestment(selectedCategory)}
                  sx={{
                    bgcolor: selectedCategoryInfo?.color,
                    "&:hover": { bgcolor: selectedCategoryInfo?.color },
                    flexShrink: 0,
                  }}
                >
                  Add {selectedCategoryInfo?.name}
                </Button>
              </Box>

              <Box
                sx={{
                  display: "grid",
                  gridTemplateColumns: "repeat(auto-fit, minmax(200px, 1fr))",
                  gap: 3,
                  width: "100%",
                }}
              >
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "flex-start",
                  }}
                >
                  <Typography variant="body2" sx={{ color: "#aaa", mb: 1 }}>
                    Total Value
                  </Typography>
                  <Typography
                    variant="h4"
                    sx={{ fontWeight: 700, color: "white" }}
                  >
                    {formatCompactCurrency(selectedCategoryData.totalValue)}
                  </Typography>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "flex-start",
                  }}
                >
                  <Typography variant="body2" sx={{ color: "#aaa", mb: 1 }}>
                    Total Returns
                  </Typography>
                  <Typography
                    variant="h4"
                    sx={{
                      fontWeight: 700,
                      color:
                        selectedCategoryData.returnPercentage >= 0
                          ? "#4caf50"
                          : "#f44336",
                    }}
                  >
                    {formatPercentage(selectedCategoryData.returnPercentage)}
                  </Typography>
                </Box>
                <Box
                  sx={{
                    display: "flex",
                    flexDirection: "column",
                    alignItems: "flex-start",
                  }}
                >
                  <Typography variant="body2" sx={{ color: "#aaa", mb: 1 }}>
                    Investments
                  </Typography>
                  <Typography
                    variant="h4"
                    sx={{ fontWeight: 700, color: "white" }}
                  >
                    {selectedCategoryData.items.length}
                  </Typography>
                </Box>
              </Box>
            </Box>

            {/* Investment List */}
            <Box
              sx={{
                p: 3,
                flex: 1,
                overflow: "auto",
              }}
            >
              {selectedCategoryData.items.length > 0 ? (
                <InvestmentList>
                  {selectedCategoryData.items.map((item, index) => (
                    <React.Fragment key={item.id}>
                      <InvestmentListItem
                        sx={{
                          px: 0,
                          py: 2,
                          flexDirection: "column",
                          alignItems: "flex-start",
                        }}
                      >
                        <Box
                          sx={{
                            display: "flex",
                            alignItems: "center",
                            width: "100%",
                            mb: 1,
                            gap: 2,
                          }}
                        >
                          <InvestmentListItemAvatar>
                            <Avatar
                              sx={{
                                bgcolor: selectedCategoryInfo?.color,
                                width: 48,
                                height: 48,
                                fontSize: "1.2rem",
                              }}
                            >
                              {selectedCategoryInfo?.icon}
                            </Avatar>
                          </InvestmentListItemAvatar>
                          <InvestmentListItemText
                            primary={
                              <Typography
                                variant="h6"
                                sx={{ fontWeight: 600, color: "white" }}
                              >
                                {item.name}
                              </Typography>
                            }
                            secondary={
                              <Typography
                                variant="body2"
                                sx={{ color: "#aaa" }}
                              >
                                Invested: {formatCurrency(item.investedAmount)}
                              </Typography>
                            }
                            sx={{ flex: 1, minWidth: 0 }}
                          />
                          <Box sx={{ display: "flex", gap: 1, flexShrink: 0 }}>
                            <InvestmentIconButton
                              onClick={() => handleViewTransactionHistory(item)}
                              sx={{ color: "#f0a500" }}
                              title="View Transaction History"
                            >
                              <History />
                            </InvestmentIconButton>
                            <InvestmentIconButton
                              onClick={(e) =>
                                handleInvestmentMenuOpen(e, item.id)
                              }
                              sx={{ color: "#aaa" }}
                            >
                              <MoreVert />
                            </InvestmentIconButton>
                          </Box>
                        </Box>
                        <Box
                          sx={{
                            display: "grid",
                            gridTemplateColumns: "1fr auto 1fr",
                            gap: 3,
                            width: "100%",
                            alignItems: "start",
                          }}
                        >
                          <Box
                            sx={{
                              display: "flex",
                              flexDirection: "column",
                              alignItems: "flex-start",
                            }}
                          >
                            <Typography
                              variant="body2"
                              sx={{ color: "#aaa", mb: 1 }}
                            >
                              Invested
                            </Typography>
                            <Typography
                              variant="h6"
                              sx={{ fontWeight: 600, color: "white" }}
                            >
                              {formatCurrency(item.investedAmount)}
                            </Typography>
                          </Box>
                          <Box
                            sx={{
                              display: "flex",
                              flexDirection: "column",
                              alignItems: "center",
                            }}
                          >
                            <Typography
                              variant="body2"
                              sx={{ color: "#aaa", mb: 1 }}
                            >
                              Return
                            </Typography>
                            <Typography
                              variant="h6"
                              sx={{
                                color:
                                  item.returnPercentage >= 0
                                    ? "#4caf50"
                                    : "#f44336",
                                fontWeight: 600,
                              }}
                            >
                              {formatPercentage(item.returnPercentage)}
                            </Typography>
                          </Box>
                          <Box
                            sx={{
                              display: "flex",
                              flexDirection: "column",
                              alignItems: "flex-end",
                            }}
                          >
                            <Typography
                              variant="body2"
                              sx={{ color: "#aaa", mb: 1 }}
                            >
                              Current Value
                            </Typography>
                            <Typography
                              variant="h5"
                              sx={{
                                fontWeight: 700,
                                color: "white",
                                textShadow: "0 0 10px rgba(255,255,255,0.3)",
                                background:
                                  "linear-gradient(45deg, #fff, #f0f0f0)",
                                backgroundClip: "text",
                                WebkitBackgroundClip: "text",
                                WebkitTextFillColor: "transparent",
                              }}
                            >
                              {formatCurrency(item.currentValue)}
                            </Typography>
                          </Box>
                        </Box>
                      </InvestmentListItem>
                      {index < selectedCategoryData.items.length - 1 && (
                        <Box sx={{ height: 1, bgcolor: "#444", mx: 2 }} />
                      )}
                    </React.Fragment>
                  ))}
                </InvestmentList>
              ) : (
                <Box sx={{ textAlign: "center", py: 4 }}>
                  <Typography variant="body2" sx={{ color: "#aaa" }}>
                    No investments in this category
                  </Typography>
                </Box>
              )}
            </Box>
          </Paper>
        </Box>
      )}
    </Box>
  );
};

export default CategoriesWebSidebar;
