import { useState } from "react";
import {
  Box,
  FormControl,
  TextField,
  Button,
  Typography,
  InputAdornment,
  IconButton,
} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import { VALIDATION_ERROR, EMAIL_REGEX } from "../../constants/common";
import { SIGN_UP_FORM } from "../../constants/auth";

function SignUp({ signUpForm, setSignUpForm, handleApi, loading }) {
  const [errors, setErrors] = useState({});
  const [showPassword, setShowPassword] = useState(false);
  const [showRePassword, setShowRePassword] = useState(false);
  const [passwordCriteria, setPasswordCriteria] = useState({
    length: false,
    lowercase: false,
    uppercase: false,
    digit: false,
    specialChar: false,
  });

  const updatePasswordCriteria = (password) => {
    const criteria = {
      length: password.length >= 8,
      lowercase: /[a-z]/.test(password),
      uppercase: /[A-Z]/.test(password),
      digit: /[0-9]/.test(password),
      specialChar: /[!@#$%^&*(),.?":{}|<>]/.test(password),
    };
    setPasswordCriteria(criteria);
  };

  const isPasswordStrong = (criteria) => Object.values(criteria).every(Boolean);

  // DOB validation function
  const validateDOB = (dob) => {
    if (!dob.trim()) {
      return VALIDATION_ERROR.DOB_REQUIRED;
    }

    const selectedDate = new Date(dob);
    const today = new Date();
    const seventyYearsAgo = new Date(
      today.getFullYear() - 70,
      today.getMonth(),
      today.getDate()
    );

    // Check if date is in the future
    if (selectedDate > today) {
      return VALIDATION_ERROR.FUTURE_DOB;
    }

    // Check if date is more than 70 years back
    if (selectedDate < seventyYearsAgo) {
      return VALIDATION_ERROR.INVALID_DOB_RANGE;
    }

    return null; // Valid DOB
  };

  const validate = () => {
    const newErrors = {};

    if (!signUpForm.name.trim())
      newErrors.name = VALIDATION_ERROR.NAME_REQUIRED;
    if (!signUpForm.emailId.trim())
      newErrors.emailId = VALIDATION_ERROR.EMAIL_REQUIRED;
    else if (!EMAIL_REGEX.test(signUpForm.emailId))
      newErrors.emailId = VALIDATION_ERROR.INVALID_EMAIL_FORMAT;

    const dobError = validateDOB(signUpForm.dob);
    if (dobError) newErrors.dob = dobError;

    if (!signUpForm.password)
      newErrors.password = VALIDATION_ERROR.PASSWORD_REQUIRED;
    else if (!isPasswordStrong(passwordCriteria)) {
      newErrors.password = "Password is too weak.";
    }

    if (!signUpForm.rePassword)
      newErrors.rePassword = VALIDATION_ERROR.REPASSWORD_REQUIRED;

    if (
      signUpForm.password &&
      signUpForm.rePassword &&
      signUpForm.password !== signUpForm.rePassword
    ) {
      newErrors.rePassword = VALIDATION_ERROR.PASSWORD_NOT_MATCHED;
    }

    setErrors(newErrors);
    return Object.keys(newErrors).length === 0;
  };

  const handleChange = (e) => {
    const { name, value } = e.target;

    setSignUpForm((prev) => ({
      ...prev,
      [name]: value,
    }));

    if (name === "password") {
      updatePasswordCriteria(value);
    }

    setErrors((prev) => {
      const updatedErrors = { ...prev };
      updatedErrors[name] = "";

      if (
        name === "password" &&
        signUpForm.rePassword &&
        signUpForm.rePassword !== value
      ) {
        updatedErrors.rePassword = VALIDATION_ERROR.PASSWORD_NOT_MATCHED;
      } else if (name === "password") {
        updatedErrors.rePassword = "";
      }

      if (
        name === "rePassword" &&
        signUpForm.password &&
        signUpForm.password !== value
      ) {
        updatedErrors.rePassword = VALIDATION_ERROR.PASSWORD_NOT_MATCHED;
      } else if (name === "rePassword") {
        updatedErrors.rePassword = "";
      }

      return updatedErrors;
    });
  };

  const handleClick = async (event) => {
    event.preventDefault();
    if (!validate()) return;

    const user = {
      name: signUpForm.name,
      emailId: signUpForm.emailId,
      dob: signUpForm.dob,
      password: btoa(signUpForm.password),
    };

    handleApi(user);
  };

  const strengthScore = Object.values(passwordCriteria).filter(Boolean).length;

  return (
    <Box
      component="form"
      sx={{ display: "flex", flexDirection: "column", gap: 3 }}
      noValidate
      onSubmit={handleClick}
      autoComplete="off"
    >
      <FormControl fullWidth>
        <TextField
          id="name"
          name="name"
          label={SIGN_UP_FORM.NAME_LABEL}
          variant="outlined"
          required
          value={signUpForm.name}
          onChange={handleChange}
          error={!!errors.name}
          helperText={errors.name}
          autoComplete="off"
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <FormControl fullWidth>
        <TextField
          id="emailId"
          name="emailId"
          label={SIGN_UP_FORM.EMAIL_LABEL}
          variant="outlined"
          required
          value={signUpForm.emailId.toLowerCase()}
          onChange={handleChange}
          error={!!errors.emailId}
          helperText={errors.emailId}
          autoComplete="off"
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <FormControl fullWidth>
        <TextField
          id="dob"
          name="dob"
          label={SIGN_UP_FORM.DOB_LABEL}
          type="date"
          variant="outlined"
          required
          value={signUpForm.dob}
          onChange={handleChange}
          error={!!errors.dob}
          helperText={errors.dob}
          InputLabelProps={{ shrink: true }}
          inputProps={{
            max: new Date().toISOString().split("T")[0], // Today's date
            min: new Date(
              new Date().getFullYear() - 70,
              new Date().getMonth(),
              new Date().getDate()
            )
              .toISOString()
              .split("T")[0], // 70 years ago
          }}
          autoComplete="off"
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <FormControl fullWidth>
        <TextField
          id="password"
          name="password"
          label={SIGN_UP_FORM.PASSWORD_LABEL}
          type={showPassword ? "text" : "password"}
          variant="outlined"
          required
          value={signUpForm.password}
          onChange={handleChange}
          error={!!errors.password}
          helperText={errors.password}
          autoComplete="off"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={() => setShowPassword(!showPassword)}
                  edge="end"
                  sx={{ color: "#f0a500" }}
                >
                  {showPassword ? <VisibilityOffIcon /> : <VisibilityIcon />}
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <Box sx={{ mt: 1 }}>
        <Box
          sx={{
            height: 8,
            backgroundColor: "#e0e0e0",
            borderRadius: 2,
            overflow: "hidden",
            mb: 2,
          }}
        >
          <Box
            sx={{
              width: `${strengthScore * 20}%`,
              height: "100%",
              backgroundColor:
                strengthScore < 3
                  ? "#f44336"
                  : strengthScore < 5
                  ? "#ff9800"
                  : "#4caf50",
              transition: "width 0.3s ease",
            }}
          />
        </Box>
        <Box sx={{ mb: 3 }}>
          <Typography
            variant="body2"
            color="textSecondary"
            sx={{ mb: 1, fontWeight: 600 }}
          >
            {SIGN_UP_FORM.PASSWORD_CRITERIA.TITLE}:
          </Typography>
          <Box sx={{ display: "grid", gridTemplateColumns: "1fr 1fr", gap: 1 }}>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                sx={{
                  width: 16,
                  height: 16,
                  borderRadius: "50%",
                  backgroundColor: passwordCriteria.length
                    ? "#4caf50"
                    : "#e0e0e0",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {passwordCriteria.length ? "" : ""}
              </Box>
              <Typography
                variant="caption"
                sx={{ color: passwordCriteria.length ? "#4caf50" : "#666" }}
              >
                {SIGN_UP_FORM.PASSWORD_CRITERIA.LENGTH}
              </Typography>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                sx={{
                  width: 16,
                  height: 16,
                  borderRadius: "50%",
                  backgroundColor: passwordCriteria.lowercase
                    ? "#4caf50"
                    : "#e0e0e0",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {passwordCriteria.lowercase ? "" : ""}
              </Box>
              <Typography
                variant="caption"
                sx={{ color: passwordCriteria.lowercase ? "#4caf50" : "#666" }}
              >
                {SIGN_UP_FORM.PASSWORD_CRITERIA.LOWERCASE}
              </Typography>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                sx={{
                  width: 16,
                  height: 16,
                  borderRadius: "50%",
                  backgroundColor: passwordCriteria.uppercase
                    ? "#4caf50"
                    : "#e0e0e0",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {passwordCriteria.uppercase ? "" : ""}
              </Box>
              <Typography
                variant="caption"
                sx={{ color: passwordCriteria.uppercase ? "#4caf50" : "#666" }}
              >
                {SIGN_UP_FORM.PASSWORD_CRITERIA.UPPERCASE}
              </Typography>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <Box
                sx={{
                  width: 16,
                  height: 16,
                  borderRadius: "50%",
                  backgroundColor: passwordCriteria.digit
                    ? "#4caf50"
                    : "#e0e0e0",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {passwordCriteria.digit ? "" : ""}
              </Box>
              <Typography
                variant="caption"
                sx={{ color: passwordCriteria.digit ? "#4caf50" : "#666" }}
              >
                {SIGN_UP_FORM.PASSWORD_CRITERIA.DIGIT}
              </Typography>
            </Box>
            <Box
              sx={{
                display: "flex",
                alignItems: "center",
                gap: 1,
                gridColumn: "1 / -1",
              }}
            >
              <Box
                sx={{
                  width: 16,
                  height: 16,
                  borderRadius: "50%",
                  backgroundColor: passwordCriteria.specialChar
                    ? "#4caf50"
                    : "#e0e0e0",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                  fontSize: "12px",
                  color: "white",
                }}
              >
                {passwordCriteria.specialChar ? "" : ""}
              </Box>
              <Typography
                variant="caption"
                sx={{
                  color: passwordCriteria.specialChar ? "#4caf50" : "#666",
                }}
              >
                {SIGN_UP_FORM.PASSWORD_CRITERIA.SPECIAL}
              </Typography>
            </Box>
          </Box>
        </Box>
      </Box>

      <FormControl fullWidth>
        <TextField
          id="re-password"
          name="rePassword"
          label={SIGN_UP_FORM.CONFIRM_PASSWORD_LABEL}
          type={showRePassword ? "text" : "password"}
          variant="outlined"
          required
          value={signUpForm.rePassword}
          onChange={handleChange}
          error={!!errors.rePassword}
          helperText={errors.rePassword}
          autoComplete="off"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle confirm password visibility"
                  onClick={() => setShowRePassword(!showRePassword)}
                  edge="end"
                  sx={{ color: "#f0a500" }}
                >
                  {showRePassword ? <VisibilityOffIcon /> : <VisibilityIcon />}
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <FormControl fullWidth>
        <Button
          type="submit"
          size="large"
          disabled={loading}
          endIcon={<SendIcon />}
          variant="contained"
          sx={{
            bgcolor: "#f0a500",
            color: "black",
            py: 1.5,
            fontSize: "1.1rem",
            fontWeight: 600,
            borderRadius: 2,
            textTransform: "none",
            "&:hover": {
              bgcolor: "#d89400",
            },
            "&:disabled": {
              bgcolor: "#ccc",
              color: "#666",
            },
          }}
        >
          {loading ? SIGN_UP_FORM.SUBMIT_LOADING : SIGN_UP_FORM.SUBMIT_BUTTON}
        </Button>
      </FormControl>
    </Box>
  );
}

export default SignUp;
