import React from "react";
import {
  Box,
  Paper,
  Typography,
  List,
  ListItem,
  ListItemText,
  ListItemAvatar,
  Avatar,
  IconButton,
  Button,
  Divider,
} from "@mui/material";
import { Add, Edit, Delete } from "@mui/icons-material";

const TransactionHistory = ({
  selectedInvestment,
  transactionHistory,
  handleAddTransaction,
  handleEditTransaction,
  handleDeleteTransaction,
  formatCurrency,
  formatDate,
  getTransactionIcon,
  getTransactionColor,
}) => {
  if (!selectedInvestment) {
    return (
      <Paper
        sx={{
          borderRadius: 2,
          overflow: "hidden",
          boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
          bgcolor: "#1a1a1a",
          border: "1px solid #333",
          p: 3,
        }}
      >
        <Typography variant="body2" sx={{ color: "#aaa", textAlign: "center" }}>
          Select an investment to view transaction history
        </Typography>
      </Paper>
    );
  }

  return (
    <Paper
      sx={{
        borderRadius: 2,
        overflow: "hidden",
        boxShadow: "0 2px 8px rgba(0,0,0,0.3)",
        bgcolor: "#1a1a1a",
        border: "1px solid #333",
        height: "100%",
        display: "flex",
        flexDirection: "column",
      }}
    >
      {/* Header */}
      <Box
        sx={{
          p: 3,
          bgcolor: "#2d2d2d",
          borderBottom: "1px solid #444",
          display: "flex",
          justifyContent: "space-between",
          alignItems: "center",
          flexShrink: 0,
        }}
      >
        <Box>
          <Typography
            variant="h5"
            sx={{ fontWeight: 600, color: "white", mb: 1 }}
          >
            Transaction History
          </Typography>
          <Typography variant="body2" sx={{ color: "#aaa" }}>
            {selectedInvestment.name} - {transactionHistory.length} transactions
          </Typography>
        </Box>
        <Box sx={{ display: "flex", gap: 1 }}>
          <Button
            variant="contained"
            startIcon={<Add />}
            onClick={() => handleAddTransaction(selectedInvestment)}
            sx={{
              bgcolor: "#f0a500",
              color: "black",
              "&:hover": { bgcolor: "#d89400" },
            }}
          >
            Add Transaction
          </Button>
        </Box>
      </Box>

      {/* Transaction List */}
      <Box sx={{ flex: 1, overflow: "auto" }}>
        {transactionHistory.length > 0 ? (
          <List sx={{ p: 0 }}>
            {transactionHistory.map((transaction, index) => (
              <React.Fragment key={transaction.id}>
                <ListItem
                  sx={{
                    px: 3,
                    py: 2,
                    "&:hover": {
                      bgcolor: "#2a2a2a",
                    },
                  }}
                >
                  <ListItemAvatar>
                    <Avatar
                      sx={{
                        bgcolor: getTransactionColor(
                          transaction.transactionType
                        ),
                        width: 40,
                        height: 40,
                        fontSize: "1rem",
                      }}
                    >
                      {getTransactionIcon(transaction.transactionType)}
                    </Avatar>
                  </ListItemAvatar>
                  <ListItemText
                    primary={
                      <Box
                        sx={{ display: "flex", alignItems: "center", gap: 1 }}
                      >
                        <Typography
                          variant="subtitle1"
                          sx={{ fontWeight: 600, color: "white" }}
                        >
                          {transaction.transactionType}
                        </Typography>
                      </Box>
                    }
                    secondary={
                      <Box>
                        <Typography
                          variant="body2"
                          sx={{ color: "#aaa", mb: 0.5 }}
                        >
                          {formatDate(transaction.transactionDate)}
                        </Typography>
                        {transaction.description && (
                          <Typography
                            variant="body2"
                            sx={{ color: "#888", mb: 0.5 }}
                          >
                            {transaction.description}
                          </Typography>
                        )}
                        {transaction.quantity && (
                          <Typography variant="body2" sx={{ color: "#888" }}>
                            Quantity: {transaction.quantity}
                          </Typography>
                        )}
                      </Box>
                    }
                  />
                  <Box sx={{ textAlign: "right", mr: 2 }}>
                    <Typography
                      variant="h6"
                      sx={{
                        fontWeight: 600,
                        color: "white",
                        mb: 0.5,
                      }}
                    >
                      {formatCurrency(transaction.amount)}
                    </Typography>
                    <Typography variant="body2" sx={{ color: "#aaa" }}>
                      {transaction.transactionType === "BUY" ||
                      transaction.transactionType === "DEPOSIT"
                        ? "Debit"
                        : "Credit"}
                    </Typography>
                  </Box>
                  <IconButton
                    onClick={() =>
                      handleEditTransaction &&
                      handleEditTransaction(transaction)
                    }
                    sx={{ color: "#f0a500", mr: 1 }}
                    size="small"
                  >
                    <Edit />
                  </IconButton>
                  <IconButton
                    onClick={() =>
                      handleDeleteTransaction &&
                      handleDeleteTransaction(transaction)
                    }
                    sx={{ color: "#f44336" }}
                    size="small"
                  >
                    <Delete />
                  </IconButton>
                </ListItem>
                {index < transactionHistory.length - 1 && (
                  <Divider sx={{ bgcolor: "#444", mx: 3 }} />
                )}
              </React.Fragment>
            ))}
          </List>
        ) : (
          <Box sx={{ p: 4, textAlign: "center" }}>
            <Typography variant="body2" sx={{ color: "#aaa", mb: 2 }}>
              No transaction history available
            </Typography>
            <Button
              variant="outlined"
              startIcon={<Add />}
              onClick={() => handleAddTransaction(selectedInvestment)}
              sx={{
                borderColor: "#f0a500",
                color: "#f0a500",
                "&:hover": {
                  borderColor: "#d89400",
                  bgcolor: "rgba(240, 165, 0, 0.1)",
                },
              }}
            >
              Add First Transaction
            </Button>
          </Box>
        )}
      </Box>
    </Paper>
  );
};

export default TransactionHistory;
