import React from "react";
import {
  Box,
  Container,
  Typography,
  Paper,
  Divider,
  CircularProgress,
  TextField,
  Button,
} from "@mui/material";
import { Link } from "react-router-dom";
import OTPForm from "./OTPForm";
import { FORGOT_PASSWORD } from "../../constants/auth/ForgotPasswordConstants";
import { OTP_VALIDATION } from "../../constants/auth";

const ForgotPassword = ({
  step,
  email,
  setEmail,
  otpTimer,
  setOtpTimer,
  loading,
  newPassword,
  setNewPassword,
  confirmPassword,
  setConfirmPassword,
  handleRequestOtp,
  handleOtpValidated,
  handleResetPassword,
}) => {
  return (
    <Box
      sx={{
        minHeight: "100vh",
        bgcolor: "#f8f9fa",
        overflowX: "hidden",
        maxWidth: "100vw",
        width: "100%",
        boxSizing: "border-box",
      }}
    >
      <Box sx={{ height: "80px" }} />
      <Container maxWidth="sm" sx={{ py: 2, px: { xs: 2, sm: 3, md: 4 } }}>
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            mb: 6,
          }}
        >
          <Paper
            elevation={8}
            sx={{
              p: { xs: 3, sm: 4 },
              borderRadius: 3,
              bgcolor: "white",
              width: "100%",
              maxWidth: 500,
              boxShadow: "0 8px 32px rgba(0,0,0,0.1)",
            }}
          >
            <Box sx={{ textAlign: "center", mb: 4 }}>
              <Typography
                variant="h4"
                sx={{
                  fontWeight: 700,
                  mb: 1,
                  color: "#1e1e1e",
                  fontSize: { xs: "1.75rem", sm: "2rem", md: "2.125rem" },
                }}
              >
                {FORGOT_PASSWORD.TITLE}
              </Typography>
              <Typography
                variant="body1"
                sx={{ color: "#666", fontSize: { xs: "0.875rem", sm: "1rem" } }}
              >
                {FORGOT_PASSWORD.SUBTITLE}
              </Typography>
            </Box>
            {loading && (
              <CircularProgress sx={{ display: "block", mx: "auto", my: 2 }} />
            )}
            {step === 1 && !loading && (
              <>
                <TextField
                  label={FORGOT_PASSWORD.EMAIL_LABEL}
                  type="email"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  fullWidth
                  margin="normal"
                  required
                />
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  sx={{
                    mt: 2,
                    bgcolor: "#f0a500",
                    color: "black",
                    fontWeight: 600,
                    "&:hover": { bgcolor: "#d89400" },
                  }}
                  onClick={handleRequestOtp}
                >
                  {FORGOT_PASSWORD.SEND_OTP}
                </Button>
                <Divider sx={{ my: 3 }} />
                <Box sx={{ textAlign: "center" }}>
                  <Typography
                    variant="body2"
                    sx={{
                      color: "#666",
                      mb: 2,
                      fontSize: { xs: "0.875rem", sm: "1rem" },
                    }}
                  >
                    {FORGOT_PASSWORD.REMEMBERED_PASSWORD}{" "}
                    <Link
                      to="/signin"
                      style={{
                        color: "#f0a500",
                        textDecoration: "none",
                        fontWeight: 600,
                      }}
                    >
                      {FORGOT_PASSWORD.SIGN_IN}
                    </Link>
                  </Typography>
                </Box>
              </>
            )}
            {step === 2 && !loading && (
              <OTPForm
                timer={otpTimer}
                setTimer={setOtpTimer}
                formData={{ emailId: email }}
                handleOtpSendApi={handleRequestOtp}
                type={OTP_VALIDATION.FORGOT_PASSWORD_TYPE}
                handleApi={handleOtpValidated}
              />
            )}
            {step === 3 && !loading && (
              <>
                <TextField
                  label={FORGOT_PASSWORD.NEW_PASSWORD_LABEL}
                  type="password"
                  value={newPassword}
                  onChange={(e) => setNewPassword(e.target.value)}
                  fullWidth
                  margin="normal"
                  required
                />
                <TextField
                  label={FORGOT_PASSWORD.CONFIRM_PASSWORD_LABEL}
                  type="password"
                  value={confirmPassword}
                  onChange={(e) => setConfirmPassword(e.target.value)}
                  fullWidth
                  margin="normal"
                  required
                />
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  sx={{
                    mt: 2,
                    bgcolor: "#f0a500",
                    color: "black",
                    fontWeight: 600,
                    "&:hover": { bgcolor: "#d89400" },
                  }}
                  onClick={handleResetPassword}
                >
                  {FORGOT_PASSWORD.RESET_PASSWORD}
                </Button>
              </>
            )}
            {step === 4 && (
              <>
                <Typography
                  variant="h6"
                  sx={{ color: "#4caf50", mt: 2, mb: 2, textAlign: "center" }}
                >
                  {FORGOT_PASSWORD.RESET_SUCCESS}
                </Typography>
                <Button
                  variant="contained"
                  color="primary"
                  fullWidth
                  sx={{
                    mt: 2,
                    bgcolor: "#f0a500",
                    color: "black",
                    fontWeight: 600,
                    "&:hover": { bgcolor: "#d89400" },
                  }}
                  component={Link}
                  to="/signin"
                >
                  {FORGOT_PASSWORD.GO_TO_SIGN_IN}
                </Button>
              </>
            )}
          </Paper>
        </Box>
      </Container>
    </Box>
  );
};

export default ForgotPassword;
