import React from "react";
import {
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  TextField,
  Button,
  Box,
  Typography,
  IconButton,
  Chip,
  Autocomplete,
  useMediaQuery,
  useTheme,
} from "@mui/material";
import { Close, Add, Edit } from "@mui/icons-material";
import { DASHBOARD } from "../../../constants/dashboard";

const TransactionAmendDialogSkeleton = ({
  open,
  onClose,
  investment,
  onSave,
  editingTransaction = null,
}) => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("md"));

  const [formData, setFormData] = React.useState({
    transactionType: "",
    amount: "",
    transactionDate: new Date().toISOString().split("T")[0],
    description: "",
  });

  // Populate form data when editing
  React.useEffect(() => {
    if (editingTransaction) {
      setFormData({
        transactionType: editingTransaction.type || "",
        amount: editingTransaction.amount?.toString() || "",
        transactionDate: editingTransaction.date
          ? new Date(editingTransaction.date).toISOString().split("T")[0]
          : new Date().toISOString().split("T")[0],
        description: editingTransaction.description || "",
      });
    } else {
      // Reset form for new transaction
      setFormData({
        transactionType: "",
        amount: "",
        transactionDate: new Date().toISOString().split("T")[0],
        description: "",
      });
    }
  }, [editingTransaction]);

  const isEdit = editingTransaction !== null;
  const title = isEdit ? "Edit Transaction" : "Add Transaction";
  const buttonText = isEdit ? "Update Transaction" : "Add Transaction";

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleDateChange = (date) => {
    setFormData((prev) => ({
      ...prev,
      transactionDate: date,
    }));
  };

  const handleSave = () => {
    onSave(formData);
  };

  const transactionTypes = DASHBOARD.AUTOCOMPLETE_VALUES.transactionTypes;
  const descriptionSuggestions =
    DASHBOARD.AUTOCOMPLETE_VALUES.transactionDescriptions;

  return (
    <Dialog
      open={open}
      onClose={onClose}
      maxWidth="sm"
      fullWidth
      fullScreen={isMobile}
      PaperProps={{
        sx: {
          bgcolor: "#1a1a1a",
          borderRadius: isMobile ? 0 : 2,
          m: isMobile ? 0 : 2,
          maxHeight: isMobile ? "100vh" : "90vh",
        },
      }}
    >
      {/* Header */}
      <DialogTitle
        sx={{
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          borderBottom: "1px solid #333",
          p: isMobile ? 2 : 3,
          pb: isMobile ? 2 : 3,
        }}
      >
        <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
          {isEdit ? (
            <Edit sx={{ color: "#f0a500" }} />
          ) : (
            <Add sx={{ color: "#4caf50" }} />
          )}
          <Typography variant="h6" sx={{ fontWeight: 600, color: "white" }}>
            {title}
          </Typography>
        </Box>
        <IconButton
          onClick={onClose}
          sx={{
            color: "#aaa",
            "&:hover": { bgcolor: "rgba(255,255,255,0.1)" },
          }}
        >
          <Close />
        </IconButton>
      </DialogTitle>

      {/* Investment Info */}
      <Box
        sx={{
          p: isMobile ? 2 : 3,
          pb: isMobile ? 2 : 3,
          borderBottom: "1px solid #333",
          bgcolor: "#2d2d2d",
          display: "flex",
          alignItems: "center",
          justifyContent: "flex-start",
          gap: 2,
        }}
      >
        <Typography variant="body2" sx={{ color: "#aaa" }}>
          Investment:
        </Typography>
        <Chip
          label={investment?.name}
          sx={{
            bgcolor: "#444",
            color: "white",
            fontWeight: 600,
          }}
        />
      </Box>

      <DialogContent sx={{ p: isMobile ? 2 : 3, flex: 1 }}>
        <Box sx={{ pt: isMobile ? 0 : 1 }}>
          <Box
            sx={{
              display: "flex",
              flexDirection: "column",
              gap: isMobile ? 2 : 3,
            }}
          >
            {/* Transaction Type */}
            <Autocomplete
              options={transactionTypes}
              getOptionLabel={(option) => option.label}
              value={
                transactionTypes.find(
                  (type) => type.value === formData.transactionType
                ) || null
              }
              onChange={(event, newValue) => {
                setFormData((prev) => ({
                  ...prev,
                  transactionType: newValue ? newValue.value : "",
                }));
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Transaction Type"
                  required
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      color: "white",
                      borderRadius: 1,
                      "& fieldset": {
                        borderColor: "#444",
                      },
                      "&:hover fieldset": {
                        borderColor: "#f0a500",
                      },
                      "&.Mui-focused fieldset": {
                        borderColor: "#f0a500",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      color: "#aaa",
                      fontWeight: 500,
                      "&.Mui-focused": {
                        color: "#f0a500",
                      },
                    },
                    "& .MuiAutocomplete-input": {
                      color: "white",
                    },
                    "& .MuiAutocomplete-popupIndicator": {
                      color: "#aaa",
                    },
                  }}
                />
              )}
              renderOption={(props, option) => (
                <Box component="li" {...props}>
                  <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
                    <Box
                      sx={{
                        width: 8,
                        height: 8,
                        borderRadius: "50%",
                        bgcolor: option.color,
                      }}
                    />
                    {option.label}
                  </Box>
                </Box>
              )}
              sx={{
                "& .MuiAutocomplete-popper": {
                  "& .MuiPaper-root": {
                    bgcolor: "#2d2d2d",
                    border: "1px solid #444",
                  },
                  "& .MuiAutocomplete-option": {
                    color: "white",
                    "&:hover": {
                      bgcolor: "#444",
                    },
                    "&.Mui-focused": {
                      bgcolor: "#f0a500",
                      color: "black",
                    },
                  },
                },
              }}
            />

            {/* Amount */}
            <TextField
              name="amount"
              label="Amount ()"
              variant="outlined"
              fullWidth
              required
              type="number"
              value={formData.amount}
              onChange={handleInputChange}
              sx={{
                "& .MuiOutlinedInput-root": {
                  color: "white",
                  borderRadius: 1,
                  "& fieldset": {
                    borderColor: "#444",
                  },
                  "&:hover fieldset": {
                    borderColor: "#f0a500",
                  },
                  "&.Mui-focused fieldset": {
                    borderColor: "#f0a500",
                  },
                },
                "& .MuiInputLabel-root": {
                  color: "#aaa",
                  fontWeight: 500,
                  "&.Mui-focused": {
                    color: "#f0a500",
                  },
                },
              }}
            />

            {/* Transaction Date */}
            <TextField
              name="transactionDate"
              label="Transaction Date"
              variant="outlined"
              fullWidth
              type="date"
              value={
                formData.transactionDate
                  ? new Date(formData.transactionDate)
                      .toISOString()
                      .split("T")[0]
                  : ""
              }
              onChange={(e) => {
                const date = e.target.value
                  ? new Date(e.target.value)
                  : new Date();
                handleDateChange(date);
              }}
              InputLabelProps={{
                shrink: true,
              }}
              InputProps={{
                style: { color: "white" },
              }}
              sx={{
                "& .MuiOutlinedInput-root": {
                  color: "white",
                  borderRadius: 1,
                  cursor: "pointer",
                  "& fieldset": { borderColor: "#444" },
                  "&:hover fieldset": { borderColor: "#f0a500" },
                  "&.Mui-focused fieldset": { borderColor: "#f0a500" },
                  "& input": { color: "white", cursor: "pointer" },
                  '& input[type="date"]::-webkit-calendar-picker-indicator': {
                    filter: "invert(1)",
                    cursor: "pointer",
                  },
                  '& input[type="date"]::-webkit-datetime-edit': {
                    color: "white",
                  },
                  '& input[type="date"]::-webkit-datetime-edit-fields-wrapper':
                    {
                      color: "white",
                    },
                  '& input[type="date"]::-webkit-datetime-edit-text': {
                    color: "white",
                  },
                  '& input[type="date"]::-webkit-datetime-edit-month-field': {
                    color: "white",
                  },
                  '& input[type="date"]::-webkit-datetime-edit-day-field': {
                    color: "white",
                  },
                  '& input[type="date"]::-webkit-datetime-edit-year-field': {
                    color: "white",
                  },
                },
                "& .MuiInputLabel-root": {
                  color: "#aaa",
                  fontWeight: 500,
                  "&.Mui-focused": { color: "#f0a500" },
                },
              }}
            />

            {/* Description */}
            <Autocomplete
              freeSolo
              options={descriptionSuggestions}
              value={formData.description || ""}
              onChange={(event, newValue) => {
                setFormData((prev) => ({
                  ...prev,
                  description: newValue || "",
                }));
              }}
              onInputChange={(event, newInputValue) => {
                setFormData((prev) => ({
                  ...prev,
                  description: newInputValue,
                }));
              }}
              renderInput={(params) => (
                <TextField
                  {...params}
                  label="Description (Optional)"
                  multiline
                  rows={3}
                  sx={{
                    "& .MuiOutlinedInput-root": {
                      color: "white",
                      borderRadius: 1,
                      "& fieldset": {
                        borderColor: "#444",
                      },
                      "&:hover fieldset": {
                        borderColor: "#f0a500",
                      },
                      "&.Mui-focused fieldset": {
                        borderColor: "#f0a500",
                      },
                    },
                    "& .MuiInputLabel-root": {
                      color: "#aaa",
                      fontWeight: 500,
                      "&.Mui-focused": {
                        color: "#f0a500",
                      },
                    },
                    "& .MuiAutocomplete-input": {
                      color: "white",
                    },
                    "& .MuiAutocomplete-popupIndicator": {
                      color: "#aaa",
                    },
                  }}
                />
              )}
              sx={{
                "& .MuiAutocomplete-popper": {
                  "& .MuiPaper-root": {
                    bgcolor: "#2d2d2d",
                    border: "1px solid #444",
                  },
                  "& .MuiAutocomplete-option": {
                    color: "white",
                    "&:hover": {
                      bgcolor: "#444",
                    },
                    "&.Mui-focused": {
                      bgcolor: "#f0a500",
                      color: "black",
                    },
                  },
                },
              }}
            />
          </Box>
        </Box>
      </DialogContent>

      {/* Actions */}
      <DialogActions
        sx={{
          borderTop: "1px solid #333",
          bgcolor: "#2d2d2d",
          p: isMobile ? 2 : 3,
          gap: 2,
          flexDirection: isMobile ? "column" : "row",
        }}
      >
        <Button
          onClick={onClose}
          variant="outlined"
          fullWidth={isMobile}
          sx={{
            color: "#aaa",
            borderColor: "#444",
            px: isMobile ? 2 : 3,
            py: isMobile ? 1.5 : 1,
            "&:hover": {
              borderColor: "#aaa",
              bgcolor: "rgba(255,255,255,0.05)",
            },
          }}
        >
          Cancel
        </Button>
        <Button
          onClick={handleSave}
          variant="contained"
          fullWidth={isMobile}
          sx={{
            bgcolor: isEdit ? "#f0a500" : "#4caf50",
            color: isEdit ? "black" : "white",
            fontWeight: 600,
            px: isMobile ? 2 : 3,
            py: isMobile ? 1.5 : 1,
            "&:hover": {
              bgcolor: isEdit ? "#d89400" : "#45a049",
            },
          }}
        >
          {buttonText}
        </Button>
      </DialogActions>
    </Dialog>
  );
};

export default TransactionAmendDialogSkeleton;
