import { apiBaseUrl, apiFetchPortfolioPath } from "../../constants/common";

// Fetch portfolio data
export async function fetchPortfolio() {
  const token = localStorage.getItem("sessionToken");
  if (!token) {
    console.warn("No session token found");
    return null;
  }

  try {
    const response = await fetch(`${apiBaseUrl}${apiFetchPortfolioPath}`, {
      method: "GET",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
    });

    if (!response.ok) {
      console.error("Failed to fetch portfolio:", response.status);
      return null;
    }

    return await response.json();
  } catch (error) {
    console.error("Error fetching portfolio:", error);
    return null;
  }
}

class UserDataService {
  constructor() {
    this.data = null;
  }

  async loadData() {
    this.data = await fetchPortfolio();
    return this.data !== null;
  }

  // Get user information
  getUser() {
    if (!this.data) return null;
    return this.data.user;
  }

  // Get portfolio summary
  getPortfolioSummary() {
    if (!this.data) return null;
    return this.data.portfolio.summary;
  }

  // Get asset allocation data for pie chart
  getAssetAllocation() {
    if (!this.data) return null;
    return this.data.portfolio.assetAllocation;
  }

  // Get performance history
  getPerformanceHistory() {
    if (!this.data) return null;
    return this.data.portfolio.performanceHistory;
  }

  // Get all categories
  getCategories() {
    if (!this.data) return null;
    return this.data.categories;
  }

  // Get specific category
  getCategory(categoryId) {
    if (!this.data) return null;
    return this.data.categories[categoryId];
  }

  // Get transaction history for an investment
  getTransactionHistory(investmentId) {
    if (!this.data) return null;
    return this.data.transactions[investmentId] || [];
  }

  // Search investments and categories
  searchData(query) {
    if (!query || !query.trim()) {
      return this.data.categories;
    }

    const searchTerm = query.toLowerCase().trim();
    const filteredCategories = {};

    Object.entries(this.data.categories).forEach(([categoryKey, category]) => {
      const categoryName = category.name.toLowerCase();
      const categoryDescription = category.description.toLowerCase();

      // Check if category matches
      const categoryMatches =
        categoryName.includes(searchTerm) ||
        categoryDescription.includes(searchTerm);

      // Check if any investment in this category matches
      const matchingInvestments = category.items.filter((item) => {
        const itemName = item.name.toLowerCase();
        const itemDescription = item.description.toLowerCase();

        return (
          itemName.includes(searchTerm) || itemDescription.includes(searchTerm)
        );
      });

      // Include category if it matches or has matching investments
      if (categoryMatches || matchingInvestments.length > 0) {
        filteredCategories[categoryKey] = {
          ...category,
          items: categoryMatches ? category.items : matchingInvestments,
        };
      }
    });

    return filteredCategories;
  }

  // Reset to original data or clear data on logout
  resetData() {
    this.data = null;
  }
}

// Create singleton instance
const userDataService = new UserDataService();

export default userDataService;
