import { Bounce, toast } from 'react-toastify';

export function showToast(type, message) {
  const attributes = {
    position: 'top-center',
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: false,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: 'colored',
    transition: Bounce,
  };

  if (type === 'success') toast.success(message, attributes);
  else if (type === 'error') toast.error(message, attributes);
  else if (type === 'warning') toast.warning(message, attributes);
  else toast.info(message, attributes);
}
