import React, { useContext, useState, useEffect } from "react";
import {
  AppBar,
  Box,
  Toolbar,
  IconButton,
  Typography,
  Button,
  Menu,
  MenuItem,
  useMediaQuery,
} from "@mui/material";

import AccountCircle from "@mui/icons-material/AccountCircle";
import { useTheme } from "@mui/material/styles";
import { Link, useNavigate } from "react-router-dom";
import { AuthContext } from "../../services/context/AuthContext";
import userDataService from "../../services/api/userDataService";
import {
  HEADER_NAVIGATION,
  HEADER_BUTTONS,
} from "../../constants/layout/HeaderConstants";
import { brandLogo } from "../../assets";

function Header() {
  const { isAuthenticated, logout, isLoading } = useContext(AuthContext);
  const navigate = useNavigate();
  const [userData, setUserData] = useState(null);
  const [isUserDataLoaded, setIsUserDataLoaded] = useState(false);

  const [anchorEl, setAnchorEl] = useState(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down("sm"));

  // Fetch user data when component mounts or when isAuthenticated changes
  useEffect(() => {
    if (isAuthenticated) {
      const user = userDataService.getUser();
      setUserData(user);
      setIsUserDataLoaded(true);
    } else {
      setUserData(null);
      setIsUserDataLoaded(true);
    }
  }, [isAuthenticated]);

  if (isLoading) return null;

  // Extract first name from full name
  const getFirstName = (fullName) => {
    if (!fullName) return "";
    return fullName.split(" ")[0];
  };

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleLogout = () => {
    logout();
    navigate("/signin");
  };

  const handleMenuItemClick = (action) => {
    handleMenuClose();
    if (action && action.toLowerCase() === "logout") {
      handleLogout();
    }
  };

  return (
    <React.Fragment>
      <AppBar
        position="fixed"
        sx={{
          background: "linear-gradient(135deg, #1e1e1e 0%, #2d2d2d 100%)",
          padding: isMobile ? "10px 0" : "0",
          boxShadow: "0 2px 20px rgba(0,0,0,0.1)",
        }}
      >
        <Toolbar sx={{ justifyContent: "space-between" }}>
          {!isMobile && (
            <Box sx={{ display: "flex", alignItems: "center" }}>
              <Typography variant="h6" sx={{ ml: isAuthenticated ? 1 : 0 }}>
                <Link to="/">
                  <img
                    src={brandLogo}
                    alt="Money Trails"
                    width={180}
                    style={{ verticalAlign: "middle", margin: 10 }}
                  />
                </Link>
              </Typography>
            </Box>
          )}

          {isMobile && (
            <Typography variant="h6" sx={{ textAlign: "center" }}>
              <Link to="/">
                <img
                  src={brandLogo}
                  alt="logo"
                  width={140}
                  style={{ verticalAlign: "middle", marginRight: 8 }}
                />
              </Link>
            </Typography>
          )}

          <Box>
            {!isAuthenticated ? (
              <>
                <Button
                  variant="outlined"
                  component={Link}
                  to="/signup"
                  sx={{
                    marginRight: "15px",
                    borderColor: "#f0a500",
                    color: "#f0a500",
                    fontWeight: 600,
                    "&:hover": {
                      borderColor: "#d89400",
                      color: "#d89400",
                    },
                  }}
                >
                  {HEADER_BUTTONS.SIGN_UP}
                </Button>
                <Button
                  variant="contained"
                  component={Link}
                  to="/signin"
                  sx={{
                    bgcolor: "#f0a500",
                    color: "black",
                    fontWeight: 600,
                    "&:hover": {
                      bgcolor: "#d89400",
                    },
                  }}
                >
                  {HEADER_BUTTONS.SIGN_IN}
                </Button>
              </>
            ) : (
              <>
                <Box
                  sx={{
                    display: "flex",
                    alignItems: "center",
                    gap: 1,
                    mr: 1,
                  }}
                >
                  <Typography
                    variant="body1"
                    sx={{
                      color: "white",
                      fontWeight: 800,
                      fontSize: { xs: "1.2rem", sm: "1.5rem" },
                      textTransform: "capitalize",
                    }}
                  >
                    Hi, {getFirstName(userData?.name)}
                  </Typography>
                  <IconButton
                    color="inherit"
                    onClick={handleMenuOpen}
                    sx={{
                      "& .MuiSvgIcon-root": {
                        fontSize: { xs: "2rem", sm: "2.5rem" },
                      },
                    }}
                  >
                    <AccountCircle />
                  </IconButton>
                </Box>
                <Menu
                  anchorEl={anchorEl}
                  open={Boolean(anchorEl)}
                  onClose={handleMenuClose}
                >
                  <MenuItem
                    onClick={() =>
                      handleMenuItemClick(HEADER_NAVIGATION.PROFILE)
                    }
                  >
                    {HEADER_NAVIGATION.PROFILE}
                  </MenuItem>
                  <MenuItem
                    onClick={() =>
                      handleMenuItemClick(HEADER_NAVIGATION.CHANGE_PASSWORD)
                    }
                  >
                    {HEADER_NAVIGATION.CHANGE_PASSWORD}
                  </MenuItem>
                  <MenuItem
                    onClick={() =>
                      handleMenuItemClick(HEADER_NAVIGATION.LOGOUT)
                    }
                  >
                    {HEADER_NAVIGATION.LOGOUT}
                  </MenuItem>
                </Menu>
              </>
            )}
          </Box>
        </Toolbar>
      </AppBar>
    </React.Fragment>
  );
}

export default Header;
