import { createContext, useState, useEffect } from "react";
import { validateToken } from "../api";
import userDataService from "../api/userDataService";

export const AuthContext = createContext();

export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    const checkAuth = async () => {
      try {
        const token = localStorage.getItem("sessionToken");
        const emailId = localStorage.getItem("emailId");

        if (!token || !emailId) {
          setIsAuthenticated(false);
          setIsLoading(false);
          return;
        }

        const response = await validateToken();
        if (response.ok) {
          setIsAuthenticated(true);
        } else {
          localStorage.clear();
          setIsAuthenticated(false);
        }
      } catch (error) {
        localStorage.clear();
        setIsAuthenticated(false);
      } finally {
        setIsLoading(false);
      }
    };
    checkAuth();
  }, []);

  const login = (session) => {
    localStorage.setItem("emailId", session.emailId);
    localStorage.setItem("sessionToken", session.sessionToken);
    if (session.expireAt) {
      localStorage.setItem("sessionExpireAt", session.expireAt);
    }
    setIsAuthenticated(true);
  };

  // For now, logout does not call the API. Uncomment logoutApi() when backend is ready.
  const logout = () => {
    // try {
    //   await logoutApi();
    // } catch (error) {
    //   console.error('Logout API call failed:', error);
    // }
    localStorage.clear();
    userDataService.resetData();
    setIsAuthenticated(false);
  };

  return (
    <AuthContext.Provider value={{ isAuthenticated, isLoading, login, logout }}>
      {children}
    </AuthContext.Provider>
  );
};
