import {
  Box,
  FormControl,
  Button,
  TextField,
  InputAdornment,
  IconButton,
} from "@mui/material";
import SendIcon from "@mui/icons-material/Send";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import { useState } from "react";
import { VALIDATION_ERROR, EMAIL_REGEX } from "../../constants/common";
import { SIGN_IN_FORM } from "../../constants/auth";
import { Link } from "react-router-dom";

const SignIn = ({ setSignInForm, signInForm, handleApi }) => {
  const [errors, setErrors] = useState({});
  const [loading, setLoading] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  // Validation
  const validate = () => {
    const newErrors = {};

    if (!signInForm.emailId.trim())
      newErrors.emailId = VALIDATION_ERROR.EMAIL_REQUIRED;
    else if (!EMAIL_REGEX.test(signInForm.emailId))
      newErrors.emailId = VALIDATION_ERROR.INVALID_EMAIL_FORMAT;
    if (!signInForm.password)
      newErrors.password = VALIDATION_ERROR.PASSWORD_REQUIRED;

    setErrors(newErrors);

    return Object.keys(newErrors).length === 0; // no errors means valid
  };

  const handleChange = (e) => {
    setSignInForm((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));

    // Clear error on field change
    setErrors((prev) => ({ ...prev, [e.target.name]: "" }));
  };

  // Submit Button handler
  const handleClick = async () => {
    if (!validate()) return;

    setLoading(true);

    const user = {
      emailId: signInForm.emailId,
      password: btoa(signInForm.password),
    };

    // Sign in api call
    await handleApi(user);
    setLoading(false);
  };

  return (
    <Box
      component="form"
      onSubmit={(e) => {
        e.preventDefault();
        handleClick();
      }}
      sx={{ display: "flex", flexDirection: "column", gap: 3 }}
      autoComplete="off"
      noValidate
    >
      <FormControl fullWidth>
        <TextField
          id="emailId"
          name="emailId"
          label={SIGN_IN_FORM.EMAIL_LABEL}
          variant="outlined"
          required
          value={signInForm.emailId.toLowerCase()}
          onChange={handleChange}
          error={!!errors.emailId}
          helperText={errors.emailId}
          autoComplete="off"
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <FormControl fullWidth>
        <TextField
          id="password"
          name="password"
          label={SIGN_IN_FORM.PASSWORD_LABEL}
          type={showPassword ? "text" : "password"}
          variant="outlined"
          required
          value={signInForm.password}
          onChange={handleChange}
          error={!!errors.password}
          helperText={errors.password}
          autoComplete="off"
          InputProps={{
            endAdornment: (
              <InputAdornment position="end">
                <IconButton
                  aria-label="toggle password visibility"
                  onClick={() => setShowPassword(!showPassword)}
                  edge="end"
                  sx={{ color: "#f0a500" }}
                >
                  {showPassword ? <VisibilityOffIcon /> : <VisibilityIcon />}
                </IconButton>
              </InputAdornment>
            ),
          }}
          sx={{
            "& .MuiOutlinedInput-root": {
              borderRadius: 2,
              "&:hover fieldset": {
                borderColor: "#f0a500",
              },
              "&.Mui-focused fieldset": {
                borderColor: "#f0a500",
              },
            },
          }}
        />
      </FormControl>

      <Box sx={{ textAlign: "right", mb: 2 }}>
        <Link
          to="/forgot-password"
          style={{
            color: "#f0a500",
            textDecoration: "none",
            fontWeight: 600,
            fontSize: "0.95rem",
          }}
        >
          {SIGN_IN_FORM.FORGOT_PASSWORD}
        </Link>
      </Box>

      <FormControl fullWidth>
        <Button
          type="submit"
          size="large"
          disabled={loading}
          endIcon={<SendIcon />}
          variant="contained"
          sx={{
            bgcolor: "#f0a500",
            color: "black",
            py: 1.5,
            fontSize: "1.1rem",
            fontWeight: 600,
            borderRadius: 2,
            textTransform: "none",
            "&:hover": {
              bgcolor: "#d89400",
            },
            "&:disabled": {
              bgcolor: "#ccc",
              color: "#666",
            },
          }}
        >
          {loading ? SIGN_IN_FORM.SUBMIT_LOADING : SIGN_IN_FORM.SUBMIT_BUTTON}
        </Button>
      </FormControl>
    </Box>
  );
};

export default SignIn;
