import React, { useContext, useState } from "react";
import SignUp from "../components/auth/SignUp";
import OTPForm from "../components/auth/OTPForm";
import {
  OTP_VALIDATE_PAGE,
  TOAST_MESSAGE,
  TOAST_MESSAGE_TYPE,
} from "../constants/common";
import {
  SIGN_UP_FORM,
  SIGN_UP_FOOTER,
  SIGN_UP_BENEFITS,
  SIGN_UP_TRUST_INDICATORS,
} from "../constants/auth";
import {
  Box,
  Container,
  Typography,
  Grid,
  Card,
  CardContent,
  Paper,
  Divider,
  CircularProgress,
} from "@mui/material";
import {
  CheckCircle,
  Analytics,
  Security,
  Lightbulb,
} from "@mui/icons-material";
import SmartphoneIcon from "@mui/icons-material/Smartphone";
import { Link, Navigate } from "react-router-dom";
import { sendOtp, signUp } from "../services/api";
import { showToast } from "../utils/toast";
import { AuthContext } from "../services/context/AuthContext";

const SignUpPage = () => {
  const [signUpForm, setSignUpForm] = useState({
    name: "",
    emailId: "",
    dob: "",
    password: "",
    rePassword: "",
  });
  const [otpInputVisible, setOtpInputVisible] = useState(false);
  const [resendTimer, setResendTimer] = useState(0);
  const [loading, setLoading] = useState(false);
  const { isAuthenticated, isLoading } = useContext(AuthContext);

  async function handleSignUp(userRequest) {
    setLoading(true);
    try {
      const response = await signUp(userRequest);
      if (!response.ok) {
        const errorData = await response.json();
        showToast(TOAST_MESSAGE_TYPE.TYPE_WARNING, errorData.errorMessage);
        setLoading(false);
      } else {
        if (!userRequest.otp?.code || !/^\d{6}$/.test(userRequest.otp.code)) {
          handleSendOtpApi({
            emailId: signUpForm.emailId,
          });
        }
      }
      return response;
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGE.API_ERROR);
    }
    setLoading(false);
  }

  async function handleSendOtpApi(request) {
    try {
      const response = await sendOtp(request);
      if (!response.ok) {
        const errorData = await response.json();
        showToast(TOAST_MESSAGE_TYPE.TYPE_WARNING, errorData.errorMessage);
      } else {
        setOtpInputVisible(true);
        showToast(
          TOAST_MESSAGE_TYPE.TYPE_SUCCESS,
          TOAST_MESSAGE.SUCCESS_OTP_SEND
        );
        const data = await response.json();
        setResendTimer(data.expireSec);
      }
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGE.API_ERROR);
    }
  }

  const benefits = SIGN_UP_BENEFITS.BENEFITS;

  // Function to render Material UI icon based on the icon name
  const renderIcon = (iconName) => {
    const iconProps = {
      sx: { fontSize: { xs: "2.5rem", sm: "3rem" }, color: "#f0a500" },
    };

    switch (iconName) {
      case "Security":
        return <Security {...iconProps} />;
      case "Analytics":
        return <Analytics {...iconProps} />;
      case "Lightbulb":
        return <Lightbulb {...iconProps} />;
      case "Smartphone":
        return <SmartphoneIcon {...iconProps} />;
      default:
        return <span style={{ fontSize: "2.5rem" }}>{iconName}</span>;
    }
  };

  // If signed in then redirected to dashboard
  if (isLoading) {
    return <CircularProgress />;
  }
  if (isAuthenticated) {
    return <Navigate to="/dashboard" replace />;
  }
  return (
    <Box
      sx={{
        minHeight: "100vh",
        bgcolor: "#f8f9fa",
        overflowX: "hidden",
        maxWidth: "100vw",
        width: "100%",
        boxSizing: "border-box",
      }}
    >
      {/* Header Spacer */}
      <Box sx={{ height: "80px" }} />

      <Container maxWidth="xl" sx={{ py: 2, px: { xs: 2, sm: 3, md: 4 } }}>
        {/* Form Section */}
        <Box
          sx={{
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            mb: 6,
          }}
        >
          <Paper
            elevation={8}
            sx={{
              p: { xs: 3, sm: 4 },
              borderRadius: 3,
              bgcolor: "white",
              width: "100%",
              maxWidth: 500,
              boxShadow: "0 8px 32px rgba(0,0,0,0.1)",
            }}
          >
            <Box sx={{ textAlign: "center", mb: 4 }}>
              <Typography
                variant="h4"
                sx={{
                  fontWeight: 700,
                  mb: 1,
                  color: "#1e1e1e",
                  fontSize: { xs: "1.75rem", sm: "2rem", md: "2.125rem" },
                }}
              >
                {SIGN_UP_FORM.TITLE}
              </Typography>
              <Typography
                variant="body1"
                sx={{
                  color: "#666",
                  fontSize: { xs: "0.875rem", sm: "1rem" },
                }}
              >
                {SIGN_UP_FORM.SUBTITLE}
              </Typography>
            </Box>

            {!otpInputVisible ? (
              <SignUp
                signUpForm={signUpForm}
                setSignUpForm={setSignUpForm}
                handleApi={handleSignUp}
                loading={loading}
                setLoading={setLoading}
              />
            ) : (
              <OTPForm
                type={OTP_VALIDATE_PAGE.SIGN_UP_TYPE}
                formData={signUpForm}
                handleOtpSendApi={handleSendOtpApi}
                handleApi={handleSignUp}
                timer={resendTimer}
                setTimer={setResendTimer}
              />
            )}

            <Divider sx={{ my: 3 }} />

            <Box sx={{ textAlign: "center" }}>
              <Typography
                variant="body2"
                sx={{
                  color: "#666",
                  mb: 2,
                  fontSize: { xs: "0.875rem", sm: "1rem" },
                }}
              >
                {SIGN_UP_FOOTER.EXISTING_USER}{" "}
                <Link
                  to="/signin"
                  style={{
                    color: "#f0a500",
                    textDecoration: "none",
                    fontWeight: 600,
                  }}
                >
                  {SIGN_UP_FOOTER.LOGIN_LINK}
                </Link>
              </Typography>
              <Typography
                variant="caption"
                sx={{
                  color: "#999",
                  display: "block",
                  lineHeight: 1.5,
                  fontSize: { xs: "0.75rem", sm: "0.875rem" },
                }}
              >
                {SIGN_UP_FOOTER.TERMS_TEXT}{" "}
                <a
                  href="https://trails.click/legal/"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{
                    color: "#f0a500",
                    textDecoration: "none",
                  }}
                >
                  {SIGN_UP_FOOTER.TERMS_LINK}
                </a>{" "}
                {SIGN_UP_FOOTER.AND_TEXT}{" "}
                <a
                  href="https://trails.click/privacy/"
                  target="_blank"
                  rel="noopener noreferrer"
                  style={{
                    color: "#f0a500",
                    textDecoration: "none",
                  }}
                >
                  {SIGN_UP_FOOTER.PRIVACY_LINK}
                </a>
              </Typography>
            </Box>
          </Paper>
        </Box>

        {/* Benefits Section - Full Width Below */}
        <Box sx={{ mt: 6 }}>
          <Card
            sx={{
              bgcolor: "white",
              borderRadius: 3,
              boxShadow: "0 4px 20px rgba(0,0,0,0.1)",
            }}
          >
            <CardContent sx={{ p: { xs: 2, sm: 3 } }}>
              <Box sx={{ textAlign: "center", mb: 3 }}>
                <Typography
                  variant="h3"
                  sx={{
                    fontWeight: 700,
                    mb: 1,
                    color: "#1e1e1e",
                    fontSize: { xs: "1.75rem", sm: "2rem", md: "2.5rem" },
                  }}
                >
                  {SIGN_UP_BENEFITS.TITLE}
                </Typography>
                <Typography
                  variant="h6"
                  sx={{
                    color: "#666",
                    maxWidth: 800,
                    mx: "auto",
                    lineHeight: 1,
                    fontSize: { xs: "1rem", sm: "1.125rem" },
                  }}
                >
                  {SIGN_UP_BENEFITS.SUBTITLE}
                </Typography>
              </Box>

              <Grid container spacing={3}>
                {benefits.map((benefit, index) => (
                  <Grid size={{ xs: 6, md: 3 }} key={index}>
                    <Box sx={{ textAlign: "center" }}>
                      <Box
                        sx={{
                          fontSize: { xs: "2rem", sm: "2.5rem" },
                          lineHeight: 1,
                          mb: 1,
                        }}
                      >
                        {renderIcon(benefit.MATERIAL_UI_ICON)}
                      </Box>
                      <Typography
                        variant="h6"
                        sx={{
                          fontWeight: 600,
                          mb: 0.5,
                          color: "#1e1e1e",
                          fontSize: { xs: "1rem", sm: "1.125rem" },
                        }}
                      >
                        {benefit.TITLE}
                      </Typography>
                      <Typography
                        variant="body2"
                        sx={{
                          color: "#666",
                          lineHeight: 1.4,
                          fontSize: { xs: "0.875rem", sm: "1rem" },
                        }}
                      >
                        {benefit.DESCRIPTION}
                      </Typography>
                    </Box>
                  </Grid>
                ))}
              </Grid>
            </CardContent>
          </Card>
        </Box>

        {/* Trust Indicators */}
        <Box sx={{ textAlign: "center", mt: 3 }}>
          <Typography
            variant="body2"
            sx={{
              color: "#666",
              mb: 1,
              fontSize: { xs: "0.875rem", sm: "1rem" },
            }}
          >
            {SIGN_UP_TRUST_INDICATORS.TITLE}
          </Typography>
          <Box
            sx={{
              display: "flex",
              justifyContent: "center",
              gap: { xs: 1, sm: 2 },
              flexWrap: "wrap",
              flexDirection: { xs: "column", sm: "row" },
              alignItems: "center",
            }}
          >
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <CheckCircle
                sx={{ color: "#4caf50", fontSize: { xs: 18, sm: 20 } }}
              />
              <Typography
                variant="body2"
                sx={{ color: "#666", fontSize: { xs: "0.875rem", sm: "1rem" } }}
              >
                {SIGN_UP_TRUST_INDICATORS.SECURITY}
              </Typography>
            </Box>
            <Box sx={{ display: "flex", alignItems: "center", gap: 1 }}>
              <CheckCircle
                sx={{ color: "#4caf50", fontSize: { xs: 18, sm: 20 } }}
              />
              <Typography
                variant="body2"
                sx={{ color: "#666", fontSize: { xs: "0.875rem", sm: "1rem" } }}
              >
                {SIGN_UP_TRUST_INDICATORS.GDPR}
              </Typography>
            </Box>
          </Box>
        </Box>
      </Container>
    </Box>
  );
};

export default SignUpPage;
