import { useState, useRef, useEffect, useContext } from "react";
import {
  Box,
  TextField,
  Button,
  Typography,
  CircularProgress,
} from "@mui/material";
import { TOAST_MESSAGE_TYPE } from "../../constants/common";
import { OTP_VALIDATION, TOAST_MESSAGES } from "../../constants/auth";
import { showToast } from "../../utils/toast";
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../../services/context/AuthContext";
import { validateOtp } from "../../services/api/authService";

const OTPForm = ({
  formData,
  handleOtpSendApi,
  type,
  handleApi,
  timer,
  setTimer,
}) => {
  const navigate = useNavigate();

  const { OTP_LENGTH } = OTP_VALIDATION;
  const [otp, setOtp] = useState(Array(OTP_LENGTH).fill(""));
  const [showResendButton, setShowResendButton] = useState(false);
  const [isVerifying, setIsVerifying] = useState(false);
  const inputRefs = useRef([]);
  const { login } = useContext(AuthContext);

  const formatTime = (seconds) => {
    const m = Math.floor(seconds / 60)
      .toString()
      .padStart(2, "0");
    const s = (seconds % 60).toString().padStart(2, "0");
    return `${m}:${s}`;
  };

  useEffect(() => {
    if (timer > 0) {
      const countdown = setTimeout(() => setTimer(timer - 1), 1000);
      return () => clearTimeout(countdown);
    }
  }, [timer, setTimer]);

  const handleChange = (value, index) => {
    if (!/^\d?$/.test(value) || isVerifying) return;

    const newOtp = [...otp];
    newOtp[index] = value;
    setOtp(newOtp);
    if (value && index < OTP_LENGTH - 1) {
      inputRefs.current[index + 1].focus();
    }
    if (newOtp.every((digit) => digit !== "")) {
      verifyOTP(newOtp.join(""));
    }
  };

  const handleKeyDown = (e, index) => {
    if (e.key === "Backspace" && !otp[index] && index > 0) {
      inputRefs.current[index - 1].focus();
    }
  };

  const handleResend = async () => {
    setShowResendButton(false);
    setOtp(Array(OTP_LENGTH).fill(""));
    setIsVerifying(false);
    if (inputRefs.current[0]) {
      inputRefs.current[0].focus();
    }
    if (formData && formData.emailId) {
      await handleOtpSendApi({
        emailId: formData.emailId,
      });
    }
  };

  const resetOtp = () => {
    setOtp(Array(otp.length).fill(""));
    setIsVerifying(false);
    if (inputRefs.current[0]) {
      inputRefs.current[0].focus();
    }
  };

  useEffect(() => {
    resetOtp();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  async function verifyOTP(otp) {
    setIsVerifying(true);
    try {
      if (!formData || !formData.emailId) {
        showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, "Email data not available");
        setIsVerifying(false);
        return;
      }

      let verifyOTPRequest;
      if (type === OTP_VALIDATION.SIGN_UP_TYPE) {
        verifyOTPRequest = {
          name: formData.name,
          emailId: formData.emailId,
          dob: formData.dob,
          password: btoa(formData.password),
          otp: {
            code: otp,
          },
        };
        const response = await handleApi(verifyOTPRequest);
        if (!response.ok) {
          resetOtp();
        } else {
          navigate(`/signin`);
          showToast(
            TOAST_MESSAGE_TYPE.TYPE_SUCCESS,
            TOAST_MESSAGES.SUCCESS_SIGNUP
          );
        }
      } else if (type === OTP_VALIDATION.SIGN_IN_TYPE) {
        verifyOTPRequest = {
          emailId: formData.emailId,
          password: btoa(formData.password),
          otp: {
            code: otp,
          },
        };
        const response = await handleApi(verifyOTPRequest);
        if (!response.ok) {
          resetOtp();
        } else {
          const data = await response.json();
          await login(data);
          navigate(`/dashboard`);
          showToast(
            TOAST_MESSAGE_TYPE.TYPE_SUCCESS,
            TOAST_MESSAGES.SUCCESS_LOGIN
          );
        }
      } else if (type === OTP_VALIDATION.FORGOT_PASSWORD_TYPE) {
        // Forgot password OTP validation
        const response = await validateOtp({
          emailId: formData.emailId,
          otp: { code: otp },
        });
        if (!response.ok) {
          resetOtp();
          const errorData = await response.json();
          showToast(
            TOAST_MESSAGE_TYPE.TYPE_WARNING,
            errorData.errorMessage || "Invalid OTP"
          );
        } else {
          // Pass OTP value to parent for next step
          handleApi(otp);
          showToast(
            TOAST_MESSAGE_TYPE.TYPE_SUCCESS,
            TOAST_MESSAGES.OTP_VERIFIED
          );
        }
      }
    } catch (error) {
      showToast(TOAST_MESSAGE_TYPE.TYPE_ERROR, TOAST_MESSAGES.API_ERROR);
      setShowResendButton(true);
      setIsVerifying(false); // Re-enable textboxes on error
    }
  }

  return (
    <Box textAlign="center" mt={4}>
      <Box mb={2}>
        <TextField
          label="Email ID"
          value={formData?.emailId}
          InputProps={{
            readOnly: true,
            style: { fontSize: "20px", letterSpacing: "2px", color: "#797474" },
          }}
          variant="outlined"
          fullWidth
        />
      </Box>
      <Typography variant="h6" gutterBottom>
        Enter OTP
      </Typography>
      <Box display="flex" justifyContent="center" gap={2} mb={2}>
        {otp.map((digit, index) => (
          <TextField
            key={index}
            inputProps={{
              maxLength: 1,
              style: { textAlign: "center", fontSize: "20px" },
              inputMode: "numeric",
              pattern: "[0-9]*",
              type: "tel",
            }}
            value={digit}
            onChange={(e) => handleChange(e.target.value, index)}
            onKeyDown={(e) => handleKeyDown(e, index)}
            inputRef={(el) => (inputRefs.current[index] = el)}
            variant="outlined"
            size="small"
            sx={{ width: 48 }}
            autoComplete="off"
            disabled={isVerifying}
          />
        ))}
      </Box>
      {isVerifying && (
        <Box
          display="flex"
          justifyContent="center"
          alignItems="center"
          gap={1}
          mb={2}
        >
          <CircularProgress size={20} />
          <Typography variant="body2" color="textSecondary">
            Verifying OTP...
          </Typography>
        </Box>
      )}
      {timer > 0 && !showResendButton ? (
        <Typography variant="body2" color="textSecondary">
          Resend OTP in {formatTime(timer)}s
        </Typography>
      ) : (
        <Button
          variant="contained"
          onClick={handleResend}
          disabled={isVerifying}
        >
          {OTP_VALIDATION.RESEND_BUTTON}
        </Button>
      )}
    </Box>
  );
};

export default OTPForm;
